<template>
  <div class="side m-css">
    <nav>

       <img
    src="assets/images/bg/1.jpg"
    class="side-top-bg"
     />
      <div class="nav-wrapper" >
        <a
          href="#"
          @click="active = true"
          @mouseover="active = false"
          data-target="slide-out-mobile"
          class="zi-0 sidenav-trigger nav-left-show"
        >
          <i class="material-icons">menu</i>
        </a>
        <a 
         @click="minimized = !minimized"
        href="#" data-target="slide-out" class="nav-left-show btn-desktop left">
          <i class="material-icons">menu</i>
        </a>

        <a
          class=" left">
          <span class="center logo-text">{{instance.name}}</span>
        </a>
        <ul class="right hide-on-med-and-down-" v-if="$store.state.session.active_instance" >
          <li>
            <a
            @click="open_right()"
            >
              <i class="material-icons">search</i>
            </a>
          </li>
        
          <li v-if="$Instance.Permission.advance('user','edit')">
            <a 
            @click="open_clients()" 
            >
              <i class="material-icons">account_box</i>
            </a>
           
          </li>
          <li >
            <a 
             @click="open_right()"
             >
              <i class="material-icons">settings</i>
            </a>
          </li>
        </ul>
      </div>
    </nav>
   
  </div>
</template>

<script>
//import "./../../Main/material.less";

//import './Sides.less'
//import M from 'materialize-css'
export default {
  //M,
  name: "SideLeft",
  data() {
    return {
      minimized : false,
      desktop: -1,
      mobile: -1,
      active: false,
    };
  },
  created() {},

  mounted() {
    this.$nextTick(function () {
      window.addEventListener("resize", this.resize);
    });
    this.resize();
  },
  methods: {
    resize() {
      if (window.innerWidth > 992) {
        document
          .getElementsByTagName("body")[0]
          .classList.remove("nav-mobile-active");
      }
    },
    open_right (){
      
         document
          .getElementsByTagName("body")[0]
          .classList.add("side-right-open");
    }, open_clients (){
      if(this.$route.name != 'UserList')
      this.$router.push('/user')
    }
  },
  computed : {
    instance(){
      
      if(!this.$store.state.instances.listInstances.length){
        return {}
      }
      const instance = this.$store.state.instances.listInstances.filter(instance=>{
        return instance.instance_id == this.$store.state.session.active_instance
      })

      return instance.length ? instance[0] : {}
    }
  },
  watch: {
    active(value) {
      if (!value) {
        document
          .getElementsByTagName("body")[0]
          .classList.remove("nav-mobile-active");
      } else {
        document
          .getElementsByTagName("body")[0]
          .classList.add("nav-mobile-active");
      }
    },
    minimized(value){
      if(value){
          document
          .getElementsByTagName("body")[0]
          .classList.add("minimized");
      }else{
        document
          .getElementsByTagName("body")[0]
          .classList.remove("minimized");
      }
    },
    
  },
};
</script>