<template>
  <div class="row screen-login"
   :style="{ backgroundImage: 'url(' + imgBGPage + ')' }"
   >
    <div class="filter"></div>
    <div class="main-login col s12" id="main">
      <div class="card">
        <div class="card-image">
          <div class="bg-card"
          :style="{ backgroundImage: 'url(' + imgBGHeader + ')',backgroundPosition:'0px -100px ' }"
          ></div>
          <span class="card-title">Criar Conta</span>
        </div>
        <div class="card-content">
          <div class="row">
            <Materialert v-if="message" :message="message" registry="create" :type="type_message" :is_close="false" />
            <div class="col s12">
              <div class="row">
                <div class="input-field col s12">
                  <i class="material-icons prefix">account_circle</i>
                  <input id="icon_prefix" type="text" class="validate" v-model="email" />
                  <label for="icon_prefix">Usuário ou Email</label>
                </div>
                <div class="input-field col s12">
                  <i class="material-icons prefix">security</i>
                  <input id="icon_telephone" type="password" class="validate" v-model="password" />
                  <label for="icon_telephone">Senha</label>
                </div>

        
                 <button class="btn waves-effect waves-light right" type="submit" name="action" v-on:click="auth()">Cadastrar
                  <i class="material-icons right">send</i>
                </button>
              </div>
            </div>
          </div>
        </div>

         <div class="card-action">
            
            <router-link to="/users/auth" class="">Login</router-link>
          </div>


      </div>
    </div>
  </div>
</template>
<style lang="less">

</style>
<script>

import './Login.less'
const vue = {
    data(){
        return {
            imgBGPage : 'assets/images/bg/tecnologia.svg',
            imgBGHeader : 'assets/images/bg/network.png',
            email : '',
            password : '',
            message : '',
            type_message : 'info'
        }
    },
    created(){
     
      this.user = this.$Module.$Api.$ApiUser
      document.getElementsByTagName("body")[0].classList.remove("mobile");
      document.getElementsByTagName("body")[0].classList.remove("desktop");

      console.log(this.$Module)
      this.$Module.$Init.run(vue)
    },
    methods : {

        async auth (){
        
          const create = await this.user.CreateUser({
            email : this.email,
            username : this.email,
            password : this.password
          })
          
          if(create.error){
            this.type_message = 'error'
            this.message = create.error.message || create.error
          }else{
            
            this.$router.push(create.goto)
          }
        }

    }
};

export default vue
</script>
