<template v-if="false">
<div>

  <div class="load" v-if="inLoad">
    <span class="spinner"></span>
  </div>
</div>
</template>
<style scoped>
.load {
  float: left;
  position: fixed;
  background: rgb(0, 0, 0, 0.5);
  height: 100% !important;
  width: 100% !important;
  z-index: 10000;
}
@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

.spinner:before {
  content: "";
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  margin-top: -10px;
  margin-left: -10px;
  border-radius: 50%;
  border-top: 2px solid #07d;
  border-right: 2px solid transparent;
  animation: spinner 0.6s linear infinite;
}
</style>
<script>
const e = function (e) {
  if (e.response === true) {
    return e.response.data
  } else if (e.request === true) {
    // The request was made but no response was received
    console.error('request', e.request)
    return {
      error: {
        message: 'Verify your connection and try again!'
      }
    }
  } else if (e.message === true) {
    // Something happened in setting up the request that triggered an Error
    console.error('Error', e.message)
    return {
      error: {
        message: e.message
      }
    }
  } else {
    return {
      error: {
        message: e.message ? e.message : JSON.stringify(e)
      }
    }
  }
}

const responseApi = response => {
  if (response.alert_disable) {
    if (response.goto) {
      global.route.push(response.goto)
    }
  } else
  if (response.warning) {
    global.store.dispatch('setAlert', {
      message: response.warning.message ? response.warning.message : 'Message warning undefined!',
      type: 'warning'
    })
  } else
  if ((response && !response.error) || (response.success)) {
    global.store.dispatch('setAlert', {
      message: response.message ? response.message : 'Success request send!',
      type: 'success',
      goto: response.goto ? response.goto : false
    })
  } else {
    try {
      if (response.error) {
        global.store.dispatch('setAlert', {
          message: response.error.message ? response.error.message : 'Request error!',
          type: 'error'
        })
      } else {
        global.store.dispatch('setAlert', {
          message: 'Request error!',
          type: 'error'
        })
      }
    } catch (error) {
      global.store.dispatch('setAlert', {
        message: response || JSON.stringify({
          error,
          response
        }),
        type: 'error'
      })
    }
  }
}

export default {
  data () {
    return {
      lastRequest: null
    //  instances : null
    }
  },
  computed: {
    action () {
      return global.store.state.api.action
    },
    inLoad () {
      return global.store.state.api.inLoad
    },
    listInstances () {
      return global.store.state.instances.listInstances
    }
  },
  watch: {
    async listInstances (listInstances) {
      console.log('listInstances', listInstances)
      // this.instances = value
    },
    async action (value) {
      console.log(`action (${value}) start!`)
      if (typeof ModulesApi !== 'undefined') {
        if (value != null) {
          const ModulesApi = global.ModulesApi

          this.lastRequest = value
          console.log(`request (${this.lastRequest}) start!`, ModulesApi)
          if (typeof ModulesApi !== 'undefined') {
            for (var module in ModulesApi) {
              var { data, action } = global.store.state.api.dataSend
              if (
                typeof ModulesApi[module] !== 'undefined' &&
              typeof ModulesApi[module][action] !== 'undefined'
              ) {
                global.store.dispatch('setLoad', true)
                global._self = this
                var responseErr = null
                try {
                  const response = await ModulesApi[module][action](data)

                  try {
                    responseApi(response)
                    global.store.dispatch('setLoad', false)
                  } catch (err) {
                    responseErr = e(err).error
                    global.store.dispatch('setAlert', {
                      message: responseErr.message ? responseErr.message : 'Error request send!',
                      type: 'error'
                    })
                  }

                  global.store.dispatch('setLoad', false)
                } catch (err) {
                  setTimeout(() => {
                    responseErr = e(err).error
                    global.store.dispatch('setAlert', {
                      message: responseErr.message ? responseErr.message : 'Error request send!',
                      type: 'error'
                    })

                    global.store.dispatch('setLoad', false)
                  }, 1000)
                }

                global.store.dispatch('callApi', {
                  action: null,
                  data: {}
                })
              }
            }
          }
        } else {
          console.log(`request (${this.lastRequest}) finish!`)
        }
      }
    },
    exception (e) {
      if (e.response) {
        return e.response.data
      } else if (e.request) {
        // The request was made but no response was received
        console.error('request', e.request)
        return {
          error: {
            message: 'Verify your connection and try again!'
          }
        }
      } else {
        // Something happened in setting up the request that triggered an Error
        console.error('Error', e.message)
        return {
          error: {
            message: JSON.stringify(e.message)
          }
        }
      }
    }
  }
}
</script>
