var StoreApi = {
  state: {
    api: {
      action: null,
      inLoad: false,
      dataSend: {

      }
    }
  },
  actions: {
    callApi: function ({ commit }, data) {
      commit('ApiActionData', data)
    },
    setLoad ({ commit }, value) {
      commit('setLoad', value)
    }
  },
  mutations: {
    ApiActionData (state, data) {
      state.api.action = data.action
      state.api.dataSend = { ...data }
    },
    setLoad (state, value) {
      state.api.inLoad = value
    }

  }
}

module.exports = {
  StoreApi
}
