<template>
  <div class="row screen-login"
   :style="{ backgroundImage: 'url(' + imgBGPage + ')' }"
   >
    <div class="filter">

    
    </div>
    <div class="main-login col s12" id="main">
      <div class="card">
        <div class="card-image">
          <div class="bg-card"
          :style="{ backgroundImage: 'url(' + imgBGHeader + ')',backgroundPosition:'0px -100px ' }"
          ></div>
          <span class="card-title">Login</span>
        </div>
        <div class="padding-5">
          <Materialert v-if="message" :message="message" registry="login" :type="type_message" :is_close="false" />
        </div>
        <div class="card-content">
                   
          <div class="row">
            
            <div class="col s12">

              <div class="row">
                <div class="input-field col s12">
                  <i class="material-icons prefix">account_circle</i>
                  <input id="icon_prefix" type="text" class="validate" v-model="username" />
                  <label for="icon_prefix">Usuário ou Email</label>
                </div>
                <div class="input-field col s12">
                  <i class="material-icons prefix">security</i>
                  <input id="icon_telephone" type="password" class="validate" v-model="password" />
                  <label for="icon_telephone">Senha</label>
                </div>

        
                 <button class="btn waves-effect waves-light right" type="submit" name="action" v-on:click="auth()">Logar
                  <i class="material-icons right">send</i>
                </button>
                  
              </div>

              
            </div>
            
          </div>
          
        </div>

        <div class="card-action">
                <a href="#">Recuperar Password</a>
                <router-link to="/users/create" class="right">Criar Conta</router-link>
              </div>
      </div>
    </div>
  </div>
</template>
<style lang="less">

</style>
<script>

var calc;
function onresize(){
  if(!document.getElementById('main') || document.getElementById('main').offsetHeight) return
    var offsetHeight = document.getElementById('main').offsetHeight;
    calc = (window.outerWidth / 2) - (offsetHeight / 2)

    //document.getElementById('main').style.marginTop="calc("+calc+"px / 2)"
    console.log(offsetHeight)
}
window.onload = () => {
  onresize()
}
window.addEventListener("hashchange", function(){
    onresize()
}, false);

import './Login.less'
const vue = {
    data(){
        return {
            imgBGPage : 'assets/images/bg/tecnologia.svg',
            imgBGHeader : 'assets/images/bg/network.png',
            user : {},
            username : '',
            password : '',
            message : '',
            type_message : 'error'
        }
    },
    created(){
      
      this.user = this.$Module.$Api.$ApiUser
      this.instance = this.$Module.$Api.$ApiInstance
      document.getElementsByTagName("body")[0].classList.remove("mobile");
      document.getElementsByTagName("body")[0].classList.remove("desktop");

      this.$Module.$Init.run(vue)
    },
    computed: {
      
    },
    methods : {

        async auth(){
        
        try{
            const response = await this.user.Auth({
              email : this.username,
              username : this.username,
              password : this.password
            })
            if(response.error){
                
              this.message = response.error .message|| response.error
              this.type_message = 'error'
            }
            const {origin_instance} = response

            if(origin_instance){
              debugger
              await this.instance.Instances();
              this.$router.push(`/instance/enter/${origin_instance.instance_id}/${origin_instance.user_id_instance}`)
              return
              
            }
            if(response.goto)this.$router.push(response.goto)
            
          }catch(e){
            
            this.message = 'Não foi possivel efetuar o login'
            this.type_message = 'error'
          }
         
        }

    }
};

export default vue
</script>
