const Api = require('./Api')
class ApiModule extends Api {
    async List () {
      const response = await this.get('/module')

      const { modules,modules_instance,error } = response.data
      if (error) {
        return {
          error
        }
      }
      return {
        modules,modules_instance
      }
    }
    async Update (sendData) {
      const response = await this.post('/module/update',sendData)

      const { modules,error } = response.data
      if (error) {
        return {
          error
        }
      }
      return {
        modules
      }
    }


}

export const $ApiModule = new ApiModule()
