<template>
  <div v-if="!is_close" :class="{'no-radius-top':noRadiusTop,'no-radius':noRadius}" >
    
    <div :class="`materialert default ${typeColor} ${bgColor} ${color}-text lighten-${lighten}`" :style="setStyle">
      <i class="material-icons" >check_circle</i>
      <span >{{data.text || 'Error'}}</span>
      <button v-if="!noClose" type="button" class="close-alert" @click="close()">×</button>
    </div>
  </div>
</template>

<style lang="less">
.materialert {
  position: relative;
  min-width: 150px;
  padding: 15px;
  border-radius: 4px;
  transition: all 0.1s linear;
  webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
    0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
    0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}
.radius-top .materialert{
  
}
.no-radius-top .materialert{

  border-radius: 0;
}

.no-radius .materialert{

  border-radius: 0;
}
.materialert .material-icons {
  margin-right: 10px;
}
.materialert .close-alert {
  -webkit-appearance: none;
  border: 0;
  cursor: pointer;
  color: inherit;
  background: 0 0;
  font-size: 22px;
  line-height: 1;
  font-weight: bold;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.7);
  filter: alpha(opacity=40);
  margin-bottom: -5px;
  position: absolute;
  top: 16px;
  right: 5px;
}
.materialert.default {
  background-color: #fff;
  color: #000;
}
.materialert.info {
  background-color: #039be5;
  color: #fff;
}
.materialert.success {
  background-color: #43a047;
  color: #fff;
}
.materialert.error {
  background-color: #c62828;
  color: #fff;
}
.materialert.danger {
  background-color: #c62828;
  color: #fff;
}
.materialert.warning {
  background-color: #fbc02d;
  color: #fff;
}
</style>
<script>
export default {
    props : ["message","type","registry","addclass","noRadiusTop","noRadius","noClose","setStyle","bgColor","color","lighten"],
    data(){
        return {
            text : 'a',
            local : '',
            isClose : true,
            data : {
              text : 'a',
              local : '',
              isClose : false,
            },
            noRadiosTop : false,
            typeColor : '',
            router : null
        }
    },
    created(){
      
       
        if(!this.$Instance.Materialert ){
          this.$Instance.Materialert = []
        }
        this.data.text = this.message
        this.local = this.data.local = this.registry || 'default'
        
        this.$Instance.Materialert[this.data.local] = this.data
        
        this.$store.dispatch('Materialert',this.data)

      
       
        this.typeColor = this.type || 'default'
       
    },
    computed : {
      is_close () {
       
        return this.$Instance.Materialert[this.local].isClose
      },
    },

    watch: {
      type(value){
        console.log('materialert type',value)
      }
    },
    
    methods : {
      close(){
        this.data.isClose = true
        this.$Instance.Materialert[this.data.local].isClose = true
        
        this.$store.dispatch('Materialert',this.data)

        this.$root.$emit('Materialert',this.data)

      }
    }
};
</script>

