<template>
  <div class="image-left-hide">
<li>
        <div class="user-view">
          <div class="background">
            <img class="side-left-bg" src="assets/images/bg/montain-bg.jpg" />
          </div>
          <router-link class="edit-user" :to="`/user/edit`" ><i class="material-icons">edit</i></router-link>
          <a>
            <i v-if="!user.url_image" class="circle material-icons" style="color:#000;font-size:60px;margin-top:20px;position:relative;">
              account_circle
            </i>
            <img v-if="user.url_image" class="circle" :src="user.url_image" style="background:#000" />
          </a>
          <a href="#name">
            <span class="white-text name" style="color:rgba(0, 0, 0, 0.60) !important;font-weight:bold;">
              
              {{user.username == user.email ? 'Bem vindo(a) ' : 'Bem vindo(a) ' + user.username}}</span>
          </a>
          
          <a href="#email">
            
            <span class="white-text email" style="color:rgba(0, 0, 0, 0.60) !important;font-weight:bold;">{{user.email}}</span>
          </a>
        </div>
      </li>

       <li >
        <router-link to="/">
          <i class="material-icons">home</i>Home
        </router-link>
      </li>


      <div class="" v-if="instance_is_load">
        <li v-if="$Instance.Permission.get('grade','view')">
          <router-link to="/grades">
            <i class="material-icons">av_timer</i>Grades
          </router-link>
        </li>
        <li  v-if="$Instance.Permission.get('product','view')">
          <router-link to="/products">
            <i class="material-icons">add_shopping_cart</i>Produtos
          </router-link>
        </li>
      </div>

       <li v-if="connections">
        <router-link to="/instance">
          <i class="material-icons">business</i>Conexões
        </router-link>
      </li>
      <li>
        <div class="divider"></div>
      </li>
     
      
      <li>
        <router-link class="waves-effect" to="/confirm/logout">
        <i class="material-icons">login</i>Logout</router-link>
      </li>
    </div>
</template>

<style lang="less">
.image-left-hide  {
 
  .user-view {
    margin-top:-16px;
    padding: 0px !important;
  }
  .circle  {
    display: none !important;
  }

}
.edit-user {
  position: absolute;
  bottom:0px;
  right: 0px;
  width: 45px !important;
  height: 45px !important;
  i {
    width: 45px !important;
    height: 45px !important;
    padding: 0px;
    margin: 0px;
    color: #fff;
  }
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>

<script>
export default {
    name : 'MenuLeft',
    data(){
      return {
        user_id : localStorage.getItem('user_id'),
        user : JSON.parse(localStorage.getItem('user')) || {}
      }
    },
    created(){
      
    },
    methods : {
      
    },
    computed : {
      instance_is_load(){
        return this.$store.state.session.active_instance
      },
      connections(){
      

        const listInstances = JSON.parse(localStorage.getItem('listInstances'))

        if(listInstances && listInstances.length >= 2){
          return true;
        }else{
         
          if(listInstances && listInstances.length && listInstances[0].status < 10){
            return true;
          }
          if((!listInstances || !listInstances.length) && !localStorage.getItem('active_instance')){
            return true;
          }
        }
        const user = JSON.parse(localStorage.getItem('user'))
        for(var x in listInstances){
          var instance = listInstances[x]
          if(instance.instance.userId == user.id){
            return true
          }
        }
           
        return false
      }
    }
}
</script>

<style>

</style>