<template>
  <div class="instance-create container row" >
    <!-- Cards container -->
    <div class="col s12 m12 ">
      <h4>Instancia</h4>
      <h5>Configurações</h5>

      <div class="row">
        <form class="col s12">
          <div class="row">
            <div class="input-field col s6">
              <input
                placeholder="Insira um nome"
                id="first_name"
                type="text"
                class="validate"
                v-model="instance.name"
              />
              <label for="first_name" class="active">Nome</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <input
                placeholder="Insira sua licença"
                id="first_name"
                type="text"
                class="validate"
                v-model="instance.license"
              />
              <label for="license" class="active">Licensa</label>
            </div>
          </div>

          <div class="row">
            <div class="input-field col s12">
              <textarea
                class="materialize-textarea"
                v-model="instance.description"
              ></textarea>
              <label for="email" class="active">Descrição</label>
            </div>
          </div>

    


          <div class="row col s6">
            <div class="input-field col s12" v-if="this.instance_id">
                        
              <ul class="collection with-header">
                <li class="collection-header"><h4>Domains</h4></li>
           
                <li class="collection-item" v-for="(site,index) in domain" v-bind:key="index">
                  
                  <span :class="{'green-text': site.txt,'orange-text': !site.txt}">{{site.url}}</span>
                  <i class="material-icons right blue-text pointer"  @click="edit_domain(site)">edit</i><i class="material-icons right red-text pointer"  @click="delete_domain(index)">delete</i>
                  <div class="divider"></div>
                  <ul>
                    <li class="collection-item text-info">
                      <i class="material-icons left blue-text pointer "  @click="copy_txt(site)">content_copy</i> 
                      <span>
                      INPUT DNS TXT: <br>
                      gypt.io-site-verification={{instance.instance_id}}
                      </span>
                     
                      <i class="material-icons right blue-text pointer " :class="{'rotating' : site.checking}"  @click="check_domain(site)">{{site.txt ? 'check': 'autorenew'}}</i>
                      

                      </li>
                  </ul>
                  </li>
                <li class="collection-item pointer" @click="add_domain()">Add new domain <i class="material-icons right green-text pointer">add</i></li>
              </ul>
            </div>
            <div class="input-field col s12" v-if="is_add_domain || is_edit_domain">
              <textarea
                class="materialize-textarea"
                v-model="active_domain"
              ></textarea>
              <label for="email" class="active">Domain</label>
              <i class="material-icons right green-text pointer" @click="save_domain()">save</i>
            </div>
            
          </div>


          <div class="">
            <div class="card-action">
              <a class="waves-effect waves-light btn pull-right " :class="{'disabled':checking_text}"
                @click="apply"
                ><i class="material-icons left">save</i>Aplicar</a
              >
              <div class="clearfix"></div>
            </div>
          </div>
        </form>
      </div>
    </div>
    

  </div>
</template>

<style lang="less">
.instance-create {
  
  .divider {
    margin-top:10px;
    
    margin-left:-10px;
    
    width: calc(100% + 30px);
    height: 1px;
    overflow: hidden;
    background-color: #e0e0e0;
}
  .collection.with-header .collection-item {
    padding-left: 10px;
  }
  .text-info {
    display: flex;
    align-items: center;
  }
}
@-webkit-keyframes rotating /* Safari and Chrome */ {
  from {
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes rotating {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.rotating {
  -webkit-animation: rotating 2s linear infinite;
  -moz-animation: rotating 2s linear infinite;
  -ms-animation: rotating 2s linear infinite;
  -o-animation: rotating 2s linear infinite;
  animation: rotating 2s linear infinite;
}

</style>

<script>

//import M from 'materialize-css'
export default {
  data() {
    return {
      instance: {
        name: "Minha instancia",
        description: "Minha Descrição",
        license: "Meu codigo",
        status: 1,
        config : {
          sites : []
        },
        
      },
       is_edit_domain : false,
        is_add_domain : false,
        active_domain : "",
        checking_text : false,
      api : {},
      instance_id : null
    };
  },
  created(){
    this.api = this.$Module.$Api.$ApiInstance
    this.instance_id = this.$route.params.id || null

    if(this.instance_id)this.load()
  },
  methods : {
    async apply(){
      if(!this.instance_id){
       
        var response = await this.api.Create(this.instance)
      }else{
      
        var response = await this.api.Edit(this.instance_id,this.instance)
      }
     

      if(response.goto)this.$router.push(response.goto)
    },
    async load(){
        const {instance} = await this.api.Instance(this.instance_id)
        if(instance){
          this.instance = instance
          
          if(!this.instance.config){
            this.instance.config = {}
          }
          if(!this.instance.config.sites){
            this.instance.config.sites = []
          }
        }
    },
    add_domain(){
       this.is_edit_domain = false
      this.is_add_domain = true
      
      this.active_domain =""
    },
    edit_domain(domain){
      this.is_add_domain = false
      this.is_edit_domain = domain
      this.active_domain = domain.url
    },
    delete_domain(index){
      
      this.instance.config.sites.splice(index,1)
    },
    save_domain(){
       if(this.is_edit_domain){
        this.is_edit_domain.url  = this.active_domain
      
      }else{
        
        this.instance.config.sites.push({
          url : this.active_domain
        })
        
      }
      this.is_add_domain = false
      this.is_edit_domain = false
    },
    async check_domain(site){
      if(this.checking_text)return;
      this.checking_text = site
      this.checking_text.txt = false;
      this.checking_text.checking = true
      var response = await this.api.CheckText(site,this.instance)
      setTimeout(()=>{
        debugger
        if(response.checked){
          this.checking_text.txt = true;
          M.toast({
        html:  `
        TXT Ok, now apply!
        `
      ,
      classes: 'green darken-1 rounded'});
        }else{
          M.toast({
        html:  `
        error TXT DNS to ${site.url}! <br>
        input gypt.io-site-verification=${this.instance.instance_id}
        `
      ,
      classes: 'red darken-1 rounded'});

        }
        delete this.checking_text.checking;
        this.checking_text = false
      },1000)
     
      //this.instance.config.sites[index].checking = true
    },
    copy_txt(site){
      const el = document.createElement('textarea');
      el.value = `gypt.io-site-verification=${this.instance.instance_id}`
      document.body.appendChild(el);
      el.select();
      document.execCommand('copy');
      document.body.removeChild(el);
      debugger
      M.toast({
        html:  `
        TXT copied! <br />
        ${el.value}<br />
        Input to ${site.url}
        `
      ,
      classes: 'green darken-1 rounded',});
     
    }
  },
  computed : {
    domain(){
      if(!this.instance.config.sites){
        this.instance.config.sites = []
      }
     // this.instance.config = JSON.parse(this.instance.config)
      return this.instance.config.sites
    }
  }
};
</script>