const dataAlertDefault = () => {
  return {
    open: false,
    message: '',
    type: 'info',
    onSuccess: false,
    cancel: false,
    back: false,
    goto: false
  }
}

const alerts = {
  state: {

    alert: dataAlertDefault()
  },
  actions: {
    setAlert: function ({ commit }, data) {
      commit('setAlert', data)
    },
    setAlertClose ({ commit }) {
      commit('setAlertClose')
    },
    reset ({ commit }) {
      commit('reset')
    }
  },
  mutations: {
    setAlertClose (state) {
      state.alert.open = false
    },
    setAlert (state, data) {
      for (var d in data) {
        state.alert[d] = data[d]
      }
      state.alert.open = true
    },
    reset () {
      global.store.dispatch('setAlert', dataAlertDefault())
      global.store.dispatch('setAlertClose')
    }

  }
}

module.exports = {
  MessageAlert: alerts
}
