<template>
  <div class="row module"
   :style="{ backgroundImage: 'url(' + imgBGPage + ')' }"
   >

<div id="container" v-if="!isLoad" class="wait">
  <div class="">
     <h5>Aguarde...</h5>
    <div class="progress">
      <div class="indeterminate"></div>
  </div>
        

    </div>

</div>
<div id="container" v-if="isLoad">
  <div class="row main-instances">
   
    <!-- Cards container -->
    <div class="col s12 m12 l12">

      <h4>Usuários </h4>
      <h5>Lista de usuarios</h5>
      <div id="card-container" class="row">
   
        <div class="col s12 m6 l4" v-for="(module, index) in modules" v-bind:key="index">
             <!-- Col: Card -->
        <div class="card text-red red-text" :class="{'orange lighten-4':module.not_aprove,'orange lighten-3':module.to_accept}" >
            <div class="card-content white-text">
            
             <section>
                <div class="card__meta " >
                  <a ><i class="small material-icons ">business</i> {{module.module}}</a>
                  <time>{{new Date() | DateToBRL }}</time>
                </div>

            </section>

              <span class="card-title orange-text padding-5">
                {{module.description || 'No description'}}
              </span>

              <i class="grey-text text-darken-4 padding-5" v-for="(permission,index) in module.permissions" v-bind:key="index">{{permission || '-'}}</i>

              
            </div>
            <div class="card-action ">

                <a v-if="!enable(module.module)" @click="update(module,true)"  class=" right blue-text center-center"><i class="material-icons">&nbsp;fiber_new</i>Habilitar</a>
                <a  v-if="enable(module.module)" @click="update(module,false)" class=" right blue-text center-center"><i class="material-icons">&nbsp;fiber_new</i>Desabilitar</a>
               
              
              <div class="clearfix"></div>
            </div>
          </div>
          </div>
          <!-- End of card -->
        </div>
        <!-- End of col -->
          <router-link
            class="btn-floating btn-large waves-effect waves-light red float-btn"
            to="/user/create/pin"
            ><i class="material-icons">add</i></router-link>
       
      </div>



    </div>
  </div>
</div>


</template>
<style lang="less">

.center-center {
  display: flex;
  align-items: center;
  justify-content: center;
 
}
.module {
  .wait {
    display:flex;justify-content:center;align-items:center;
    
  }
  .float-btn {
    position: absolute;
    bottom: 5px;
    right: 20px;
  }
  time {
    color: #747474b7;
  }
  .pointer {
    cursor: pointer;
  }
  .icon-add {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  @import url("https://fonts.googleapis.com/css?family=Pacifico");

  body {
    background-color: #f0f0f0;
  }
  h4,
  h5 {
    font-family: "Pacifico", cursive;
  }

  h4 {
    color: #40a8a7;
    /* Turquesa oscuro: color : #0D8080;*/
  }

  h5 {
    /*color : #40a8a7;*/
    color: #ff9900;
  }

  nav .nav-wrapper form,
  nav .nav-wrapper form .input-field {
    height: 100%;
  }

  .card-info,
  .card__meta a {
    color: #3299bb !important;
    display: inline-flex;
    vertical-align: middle;

    font-size: 1rem;
  }
  .card__meta i.small {
    font-size: 1.5rem;
  }
  .card .card-content .card-title {
    line-height: 34px;
  }
  .card__date {
    background: #08c;
    position: absolute;
    top: 6px;
    right: 6px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: #fff;
    text-align: center;
    line-height: 13px;
    font-weight: bold;
    padding-top: 7px;
  }
  .card__date__day {
    display: block;
    font-size: 14px;
  }
  .card__date__month {
    display: block;
    font-size: 10px;
    text-transform: uppercase;
  }

  .card .card-content .card-title {
  
    line-height: 18px;
  }

  .card .card-title {
    font-size: 18px;
    font-weight: 400;
  }

  .card .card-content p {
    color: #1e1e1e;
    font-size: 13px;
  }

  .collapsible {
    background: #fff;
  }

  .card-row {
    margin-bottom: 4px;
    margin-top: 4px;
  }
  .card-action i {
    vertical-align: text-bottom;
    font-size: 21px;
  }

  .collapsible-header {
    background-color: #3299bb;
    color: #fff;
  }

  .filter-container ul {
    padding: 1rem 2rem;
  }

  .filter-container ul li {
    display: block;
    padding: 16px 0 0;
  }

  .search-title-filter {
    background-color: #3299bb;
    color: #fff;
  }

  nav {
    background-color: #40a8a7;
    border-color: #e7e7e7;
  }

  nav ul > li > a,
  nav .brand-logo {
    color: #fff;
  }

  nav .brand-logo {
    font-size: 1.4em !important;
  }

  #container {
    padding: 20px 10px;

    background-color: rgba(0, 0, 0, 0.8) !important;
    height: 100%;
    overflow: auto;
    scrollbar-color: dark;
  }

  background-color: rgba(0, 0, 0) !important;
  height: 100%;
  #container .main-instances {
    margin: 0px 0px 60px;
  }
}
</style>

<script>
const vue = {
  data() {
    return {
      imgBGPage: "assets/images/bg/tecnologia.svg",
      imgBGHeader: "assets/images/bg/network.png",
      modules: [],
      modules_instance : [],
      isLoad : true,
      donor: {
        user_id : null
      },
      is_loading: false,
      permission : false
    };
  },
  created() {
    this.api = this.$Module.$Api.$ApiModule;
    this.apiInstance = this.$Module.$Api.$ApiInstance;
    this.load();
  },
  computed: {
     
  },
  methods: {
    async load() {
      this.is_loading = false;
      const response = await this.api.List();
     if(response.error) {
        
        this.$root.$emit('Materialert',{
          local : 'message',
          text : response.error.message || response.error,
          isClose : false
        })
        return;
      };
      if(response.modules){
        debugger
        this.modules = response.modules
        this.modules_instance = response.modules_instance
      }
    },
    enable(module){
      const instance = this.$store.state.instances.listInstances.find((item)=>{
        if(item.instance_id == this.$store.state.session.active_instance ){
          return item
        }
      })


      const enable = this.modules_instance.find(permission=>{
        return permission.module == module && permission.enable
      })
      
      return enable
    }
    ,async update(module,enable){
      this.isLoad = false
      module.enable = enable
      const response = await this.api.Update({
        module
      });
    
      if(response.modules){
        
        this.modules = response.modules
       
      }
       setTimeout(()=>{
         this.apiInstance.Instances()
         setTimeout(()=>{
            this.$router.go('/module')
         },3000)
       },3000)
    }
  },
};

export default vue;
</script>
