<template>
  <div
    class="row instances"
    style="background-image: url('assets/images/bg/tecnologia.svg')"
  >
    <div id="container">
      <div class="no-radius">
        <div
          class="materialert default default undefined undefined-text lighten-undefined"
        >
          <i class="material-icons">check_circle</i> <span>Volte sempre!!!</span>
    
        </div>
      </div>
      <div class="row main-instances">
        <div class="col s12 m12 l12">
        
          <div id="card-container" class="row confirm">
            <div class="cols s6 ">
              <div class="card">
                <div class="card-content white-text">
                  <!---->
                
                  <span class="card-title grey-text text-darken-4" style="line-height:initial;"
                    >Deslogando</span
                  >
                  <div class="divider"></div>
                  <p class="card-subtitle grey-text text-darken-2">

                    Vocês esta prestes a se deslogar do sistema!
                  </p>
                </div>
                <div class="card-action">
                  <!---->

                  <router-link 
                    class="right blue-text center-center" to="/logout">
                    <i class="material-icons">&nbsp;exit_to_app</i
                    >Confirmar</router-link>
                 <a
                 @click="onBack()"
                    class="left blue-text center-center" to="/">
                   <i class="material-icons">&nbsp;cancel</i>Cancelar
                    </a>
                 
                  <div class="clearfix"></div>
                </div>
                <!---->
              </div>
            </div>
          </div>
          <!---->
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
.confirm {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.message-alert {
  height: 100%;
  float: left;
  position: absolute;
  z-index: 10000;
  background: rgb(0, 0, 0, 0.8);
}
.logout {
  background: rgb(255, 255, 255);
}

.left-type-message .type-message {
  word-wrap: break-word;
}
</style>
<script>
export default {
  created() {
    window.addEventListener("keydown", (event) => {
      if (!this.open) {
        return;
      }
      if (!this.back) {
        if (event.code === "Backspace" || event.code === "Escape") {
          this.$store.dispatch("setAlertClose");
        }
      }
    });
  },
  data() {
    return {
      clickInContent: false,
    };
  },
  methods: {
    show: () => {},
    OnClickInContent() {
      this.clickInContent = true;
    },
    OnCloseBackgroud() {
      if (this.clickInContent === true) {
        this.clickInContent = false;
        return;
      }
      this.OnClose();
    },
    OnClose() {
      if (this.has_action) {
        return;
      }
      if (this.$store.state.alert.goto) {
        this.$router.push(this.$store.state.alert.goto);
      }

      this.$store.dispatch("setAlertClose");
      this.$store.dispatch("reset");
    },
    onSuccess() {
      this.success();
      this.$store.dispatch("setAlertClose");
      this.$store.dispatch("reset");
    },
    onCancel() {
      this.$store.dispatch("setAlertClose");

      this.$store.dispatch("reset");
    },

    onBack() {
      this.$store.dispatch("setAlertClose");
      this.$store.dispatch("reset");
      this.$router.go(-1);
    },
  },
  computed: {
    logout() {
      return this.$route.name === "Logout";
    },
    has_action() {
      return (
        this.$store.state.alert.cancel ||
        this.$store.state.alert.back ||
        this.$store.state.alert.onSuccess
      );
    },
    type_message() {
      return this.$store.state.alert.type;
    },
    message() {
      return this.$store.state.alert.message;
    },
    open() {
      return this.$store.state.alert.open;
    },
    cancel() {
      return this.$store.state.alert.cancel;
    },
    back() {
      return this.$store.state.alert.back;
    },
    success() {
      return this.$store.state.alert.onSuccess;
    },
  },
};
</script>
