<template>
    <div class="row products"
    
    :style="{ backgroundImage: 'url(' + imgBGPage + ')' }"
    >
        <div id="container">
          <ProductModalCreate :close="close_create" :value="value" :open_create="open_modal_create" :callback_create="callback_create"/>
          <div class="row main-instances">
            <!-- Cards container -->
              <div class="col s12 m12 l12">
                <h4>Produtos</h4>
                <h5>Lista</h5>
                <div id="card-container" class="row">
                  <div>
                    <!-- Col: Card 1 -->
                    <div
                      class="col s12 m6 l4"
                      v-for="(product, index) in products"
                      v-bind:key="index"
                    >
                      <!-- Card 1 -->
                      <div class="card">
                        <div class="card-content black-text">
                          <div class="col s12">
                         <h5>Produto</h5>
                          <span class="card-title grey-text text-darken-4">{{
                            product.name
                          }}</span>
                        <div class="divider"></div>
                        </div>

  <div class="col s6">

                          <h5>Preço</h5>
                            <span class="card-title grey-text text-darken-4">{{
                              product.price
                            }}</span>
                          <div class="divider"></div>
  </div>

  <div class="col s6">

                          <h5>Cod. Interno</h5>
                            <span class="card-title grey-text text-darken-4">{{
                              product.internal_code || '-'
                            }}</span>
                          <div class="divider"></div>
  </div>
  <div class="col s12">

   <h5>Descrição</h5>
                            <span class="card-title grey-text text-darken-4">{{
                              product.description || '-'
                            }}</span>

                        </div>
                        </div>
        
                          <div class="clearfix"></div>

                        <div class="card-action ">
                        
                          <a class="waves-effect waves-light btn pull-right" v-on:click="edit(product)"><i class="material-icons left">edit</i>Editar</a>

                          <div class="clearfix"></div>
                        </div>
                      </div>
                      <!-- End of card -->
                    </div>
                  </div>
                </div>


              <a
                  class="btn-floating btn-large waves-effect waves-light red float-btn"
                  v-on:click="create()"
                  ><i class="material-icons">add</i></a>
              </div>
          </div>
        </div>

    </div>
</template>
<style lang="less">
.products {
    .pull-right {
    float: right;
    clear: both;
  }
  .clearfix {

    clear: both;
  }
  
  .float-btn {
    position: absolute;
    bottom: 5px;
    right: 20px;
  }
  time {
    color: #222222;
  }
  @import url("https://fonts.googleapis.com/css?family=Pacifico");

  body {
    background-color: #f0f0f0;
  }
  h4,
  h5 {
    font-family: "Pacifico", cursive;
  }

  h4 {
    color: #40a8a7;
    /* Turquesa oscuro: color : #0D8080;*/
  }

  h5 {
    /*color : #40a8a7;*/
    color: #ff9900;
  }

  nav .nav-wrapper form,
  nav .nav-wrapper form .input-field {
    height: 100%;
  }

  .card-info,
  .card__meta a {
    color: #3299bb !important;
    display: inline-flex;
    vertical-align: middle;

    font-size: 1.4rem;
  }
  .card__meta i.small {
    font-size: 1.5rem;
  }
  .card .card-content .card-title {
    line-height: 34px;
  }
  .card__date {
    background: #08c;
    position: absolute;
    top: 6px;
    right: 6px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: #fff;
    text-align: center;
    line-height: 13px;
    font-weight: bold;
    padding-top: 7px;
  }
  .card__date__day {
    display: block;
    font-size: 14px;
  }
  .card__date__month {
    display: block;
    font-size: 10px;
    text-transform: uppercase;
  }

  .card .card-content .card-title {
    color: #0d8080 !important;
    line-height: 18px;
  }

  .card .card-title {
    font-size: 18px;
    font-weight: 400;
  }

  .card .card-content p {
    color: #1e1e1e;
    font-size: 13px;
  }

  .collapsible {
    background: #fff;
  }

  .card-row {
    margin-bottom: 4px;
    margin-top: 4px;
  }
  .card-action i {
    vertical-align: text-bottom;
    font-size: 21px;
  }

  .collapsible-header {
    background-color: #3299bb;
    color: #fff;
  }

  .filter-container ul {
    padding: 1rem 2rem;
  }

  .filter-container ul li {
    display: block;
    padding: 16px 0 0;
  }

  .search-title-filter {
    background-color: #3299bb;
    color: #fff;
  }

  nav {
    background-color: #40a8a7;
    border-color: #e7e7e7;
  }

  nav ul > li > a,
  nav .brand-logo {
    color: #fff;
  }

  nav .brand-logo {
    font-size: 1.4em !important;
  }

  #container {
    padding: 20px 10px;

    background-color: rgba(0, 0, 0, 0.8) !important;
    height: 100%;
    overflow: auto;
    scrollbar-color: dark;
  }

  background-color: rgba(0, 0, 0) !important;
  height: 100%;
  #container .main-instances {
    margin: 0px 0px 60px;
  }
}
</style>
<script>
import ProductModalCreate from './Modals/Create'
export default {
   components : {
    ProductModalCreate
   },
  
   data() {
    return {
      open_modal_create : false,
      imgBGPage: "assets/images/bg/tecnologia.svg",
      imgBGHeader: "assets/images/bg/network.png",
      products : [
        {
          name : "produco 1",
          description : "Descrição 1"
        }
      ],
      value : {},
      api : {}
    };
  },
    created(){
      this.api = this.$Module.$Api.$ApiProducts

      this.load()
    },
    methods : {
      async load(){
        var products = await this.api.Load();
        this.products = products
      },
      create(){
        this.value = {
          id : null
        }
        this.open_modal_create = true
      },
      edit(product){
        this.value = product
        this.open_modal_create = true
      },
      callback_create(){
        this.close_create()
        this.load()
      },
      product(){
        return this
      },
      close_create(){
        this.open_modal_create = false
      }
    }
}
</script>