var StoreInstances = {
  state: {
    instances: {
      listInstances: {}
    }
  },
  actions: {
    setInstances: function ({ commit }, data) {
      commit('setInstances', data)
    },
    setInstancePermissions: function ({ commit }, data) {
      commit('setInstancePermissions', data)
    },
    loadInstances ({ commit }) {
      commit('loadInstances')
    },
    loadInstancesPermissions ({ commit }) {
      commit('loadInstancesPermissions')
    }
  },
  mutations: {
    setInstances (state, data) {
      state.instances.listInstances = data
      localStorage.setItem('listInstances', JSON.stringify(data))
    },
    setInstancePermissions (state, data) {
      state.instances.instancePermissions = data
      localStorage.setItem('instancePermissions', JSON.stringify(data))
    },
    loadInstances () {
      const instances = localStorage.getItem('listInstances')
      if (instances) {
        global.store.dispatch('setInstances', JSON.parse(instances))
      }
    },
    loadInstancesPermissions () {
      const instances = localStorage.getItem('instancePermissions')
      if (instances) {
        global.store.dispatch('setInstancePermissions', JSON.parse(instances))
      }
    }

  }
}

module.exports = {
  StoreInstances
}
