var StoreSession = {
  state: {
    session: {
      token: null,
      active_instance: null
    }
  },
  actions: {
    
    setToken: function ({ commit }, data) {
      commit('setToken', data)
    },
    setActiveInstance: function ({ commit }, data) {
      
      commit('setActiveInstance', data)
    },
    loadToken: function ({ commit }) {
      commit('loadToken')
    },
    loadActiveInstance: function ({ commit }) {
      commit('loadActiveInstance')
    }
  },
  mutations: {
   
    setToken (state, data) {
      state.session.token = data
      localStorage.setItem('token', data)
    },
    setActiveInstance (state, {logout,instance_id,user_id_instance}) {
    if(logout){
      
      localStorage.removeItem('active_instance')
      localStorage.removeItem('user_id_instance')
      return
    }
      state.session.active_instance = instance_id
      state.session.user_id_instance = user_id_instance
      localStorage.setItem('active_instance', instance_id)
      localStorage.setItem('user_id_instance', user_id_instance)
    },
    loadToken (state) {
      state.session.token = localStorage.getItem('token')
      return state.session.token
    },
    loadActiveInstance (state) {
      state.session.active_instance = localStorage.getItem('active_instance')
    }

  }
}

module.exports = {
  StoreSession
}
