import Vue from 'vue'
import Router from 'vue-router'
import UserAuth from '@layouts/Guest/Login'
import UserGuestCreate from '@layouts/Guest/Create'

//Instances
import MainInstance from '@/src/components/instance/MainInstance'


import IntanceList from '@layouts/Instance/List'
import IntancePin from '@layouts/Instance/Pin'
import IntancePinDelete from '@layouts/Instance/Pin'
import IntanceCreate from '@layouts/Instance/Create'

import Dashboard from '@layouts/Dashboard'
import Notify from '@layouts/Notify'

import MainMaster from '@/src/components/master/MainMaster'

import MainAdonis from '@/src/components/master/MainAdonis'

import MainDocker from '@/src/components/master/MainDocker'

//User
import UserList from '@layouts/User/List'

import UserAprove from '@layouts/User/Create'
import UserEdit from '@layouts/User/Create'
import CreateUserPin from '@layouts/User/CreatePin'


//Invitation
import UserInvitation from '@layouts/Guest/Invitation'



//Module
import ModuleList from '@layouts/Module/List'


//Grades 

import GradesList from '@layouts/Grades/List'
import CreateGrade from '@layouts/Grades/Create'

import Products from '@layouts/Products/Products'

//Products 


Vue.use(Router)
// 3. Create the router instance and pass the `routes` option
// You can pass in additional options here, but let's
// keep it simple for now.
export default new Router({
    routes: [
        
        {name: "Logout", path : "/logout", component : Dashboard},
        {
            name: "Logout", path : "/confirm/logout", component : Dashboard,
            children : [
                {
                    name: "Notify", path: '', 
                    component: Notify,
                }
            ]
        },
        {name: "Home", path : "/", component : Dashboard},
        {
            name: "Grades", path : "/grades", component : Dashboard,
            children : [
                {
                    name: "GradesList", path: '', 
                    component: GradesList,
                },{
                    name: "CreateGrade", path: 'create', 
                    component: CreateGrade,
                },

            ]
        },
        {name: "All", path : "*", component : Dashboard},
        { name: "GuestLogin", path: '/login', component: UserAuth },
        
        { name: "GuestLogin", path: '/login', component: UserAuth },
        {
            name: "UserInvitation", path: '/user/invitation/:token', 
            component: UserInvitation,
        },
        
        { name: "UserAuth", path: '/users/auth', component: UserAuth },
        { name: "UserGuestCreate", path: '/users/create', component: UserGuestCreate },
        {
            name: "MainUser", path: '/user', component: Dashboard,
            children : [
                {
                    name: "UserList", path: '', 
                    component: UserList,
                },{
                    name: "UserAprove", path: 'aprove/:user_id', 
                    component: UserAprove,
                },{
                    name: "UserEdit", path: 'edit', 
                    component: UserEdit,
                },{
                    name: "UserEdit", path: 'edit/:user_id_instance', 
                    component: UserEdit,
                },{
                    name: "UserEdit", path: 'edit/:user_id', 
                    component: UserEdit,
                },{
                    name: "CreateUserPin", path: 'create/pin', 
                    component: CreateUserPin,
                },
                
            ]
        },
        { 
            name: "MainInstance", 
            path: '/instance', 
            component: Dashboard,
            children : [
                {
                    name: "IntanceList", path: '', 
                    component: IntanceList,
                }, {
                    name: "IntanceCreate", path: 'create', 
                    component: IntanceCreate,
                },{
                    name: "IntanceCreate", path: 'edit/:id', 
                    component: IntanceCreate,
                }, {
                    name: "IntanceDisconnect", path: 'disconnect', 
                    component: IntancePin,
                }, {
                    name: "IntancePin", path: 'pin/:id', 
                    component: IntancePin,
                }, {
                    name: "IntancePinCancel", path: 'pin/cancel/:id', 
                    component: IntancePin,
                }, {
                    name: "IntancePinDelete", path: 'pin/delete/:user_id', 
                    component: IntancePinDelete,
                }, {
                    name: "IntancePinConfirm", path: 'pin/confirm/:id', 
                    component: IntancePin,
                }, {
                    name: "IntanceEnter", path: 'enter/:instance_id/:user_id_instance', 
                    component: IntancePin,
                }

            ]
        },{ 
            name: "Module", path: '/module', 
            component: Dashboard,
            children : [
                {
                    name: "ModuleList", path: '', 
                    component: ModuleList,
                }

            ]
        },
        { 
            name: "Master", path: '/master', 
            component: MainMaster,
            children : [
                {
                    name: "MainDocker", path: 'docker', 
                    component: MainDocker,
                },
                {
                    name: "MainAdonis", path: 'adonis', 
                    component: MainAdonis,
                }

            ]
        },
        { 
            name: "Products", path: '/products', 
            component: Dashboard,
            children : [
                {
                    name: "ProductsList", path: '', 
                    component: Products,
                }
            ]
        },
    ]
})
