<template>
  <div class="row instances"
   :style="{ backgroundImage: 'url(' + imgBGPage + ')' }"
   >

<div id="container" v-if="is_loading">
  <div class="row main-instances">
    
    <!-- Cards container -->
    <div class="col s12 m12 l12">

      <h4>Aplicações</h4>
      <h5>Suas Conexões</h5>
      <div id="card-container" class="row">
     
        <div class="col s12 m6 l4" v-for="(instance, index) in instances" v-bind:key="index">
             <!-- Col: Card -->
        <div class="card" >
            <div class="card-content white-text">
             
             <section v-if="instance.user_id != donor.user_id">
                <div class="card__meta" v-if="instance.status_connection == 10">
                  <a href="#"><i class="small material-icons">business</i> Instance</a>
                  <time>{{new Date() | DateToBRL }}</time>
                </div>

                <div v-else-if="instance.not_connection" class="card__meta blue center" style="padding:5px;margin-bottom:2px;" >
                Efetue sua primeira conexão
                </div>
                <div v-else class="card__meta orange center" style="padding:5px;margin-bottom:2px;" >
                Aguardando aprovação
                </div>

            </section>

              <section v-if="instance.user_id == donor.user_id">
                <div class="card__meta" v-if="(instance.status == 10)">
                  <a href="#"><i class="small material-icons">business</i> Instance</a>
                  <time>{{new Date() | DateToBRL }}</time>
                </div>

                <div v-else class="card__meta orange center" style="padding:5px;margin-bottom:2px;" >
                Aguardando ativação
                </div>
            </section>

              <span class="card-title grey-text text-darken-4">{{instance.name || '-'}}</span>

              <p class="card-subtitle grey-text text-darken-2">{{instance.description || '-'}}</p>
              
            </div>
            <div class="card-action ">
              <section v-if="instance.user_id != donor.user_id">
                 <router-link v-if="instance.not_connection" :to="`/instance/pin/${instance.instance_id}`" class=" left blue-text center-center"><i class="material-icons">&nbsp;fiber_new</i>Conectar!</router-link>
                 <router-link v-else :to="`/instance/pin/cancel/${instance.instance_id}`" class=" left blue-text center-center"><i class="material-icons">&nbsp;fiber_new</i>Cancelar</router-link>
              </section>
              
              <router-link v-if="instance.user_id == donor.user_id"  :to="`/instance/edit/${instance.instance_id}`"  class=" right blue-text center-center"><i class="material-icons">&nbsp;edit</i>Editar</router-link>
              <div class="clearfix"></div>
            </div>
          </div>
          </div>
          <!-- End of card -->
        </div>
        <!-- End of col -->

        <div class="col s12 m6 l6" v-if="!instances.length && !all_instances.length">
          <!-- Card 1 -->
          <div class="card">
            <div class="card-content white-text">
             
              <div class="card__meta">
                <a href="#"><i class="small material-icons">fiber_new</i> Conecte-se</a>
                <time>{{new Date() | DateToBRL }}</time>
              </div>
              <span class="card-title grey-text text-darken-4">Conecte-se com um negocio ou crie o seu</span>

              <p class="card-subtitle grey-text text-darken-2">Aqui você pode se conectar com conegocios, criar e customizar suas proprias aplicações e muito mais...</p>
              
            </div>
            <div class="card-action ">
              <router-link to="/instance/pin" class=" left blue-text center-center hide"><i class="material-icons">&nbsp;fiber_new</i>Conectar</router-link>
              <router-link to="/instance/create" class=" right blue-text center-center"><i class="material-icons">&nbsp;fiber_new</i>Criar</router-link>
              <div class="clearfix"></div>
            </div>
          </div>
          <!-- End of card -->
        </div>
      </div>



    </div>
  </div>
</div>


</template>
<style lang="less">
.center-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.instances {
  time {
    color: #747474b7;
  }
  .pointer {
    cursor: pointer;
  }
  .icon-add {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  @import url("https://fonts.googleapis.com/css?family=Pacifico");

  body {
    background-color: #f0f0f0;
  }
  h4,
  h5 {
    font-family: "Pacifico", cursive;
  }

  h4 {
    color: #40a8a7;
    /* Turquesa oscuro: color : #0D8080;*/
  }

  h5 {
    /*color : #40a8a7;*/
    color: #ff9900;
  }

  nav .nav-wrapper form,
  nav .nav-wrapper form .input-field {
    height: 100%;
  }

  .card-info,
  .card__meta a {
    color: #3299bb !important;
    display: inline-flex;
    vertical-align: middle;

    font-size: 1rem;
  }
  .card__meta i.small {
    font-size: 1.5rem;
  }
  .card .card-content .card-title {
    line-height: 34px;
  }
  .card__date {
    background: #08c;
    position: absolute;
    top: 6px;
    right: 6px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: #fff;
    text-align: center;
    line-height: 13px;
    font-weight: bold;
    padding-top: 7px;
  }
  .card__date__day {
    display: block;
    font-size: 14px;
  }
  .card__date__month {
    display: block;
    font-size: 10px;
    text-transform: uppercase;
  }

  .card .card-content .card-title {
    color: #0d8080 !important;
    line-height: 18px;
  }

  .card .card-title {
    font-size: 18px;
    font-weight: 400;
  }

  .card .card-content p {
    color: #1e1e1e;
    font-size: 13px;
  }

  .collapsible {
    background: #fff;
  }

  .card-row {
    margin-bottom: 4px;
    margin-top: 4px;
  }
  .card-action i {
    vertical-align: text-bottom;
    font-size: 21px;
  }

  .collapsible-header {
    background-color: #3299bb;
    color: #fff;
  }

  .filter-container ul {
    padding: 1rem 2rem;
  }

  .filter-container ul li {
    display: block;
    padding: 16px 0 0;
  }

  .search-title-filter {
    background-color: #3299bb;
    color: #fff;
  }

  nav {
    background-color: #40a8a7;
    border-color: #e7e7e7;
  }

  nav ul > li > a,
  nav .brand-logo {
    color: #fff;
  }

  nav .brand-logo {
    font-size: 1.4em !important;
  }

  #container {
    padding: 20px 10px;

    background-color: rgba(0, 0, 0, 0.8) !important;
    height: 100%;
    overflow: auto;
    scrollbar-color: dark;
  }

  background-color: rgba(0, 0, 0) !important;
  height: 100%;
  #container .main-instances {
    margin: 0px 0px 60px;
  }
}
</style>

<script>
const vue = {
  data() {
    return {
      imgBGPage: "assets/images/bg/tecnologia.svg",
      imgBGHeader: "assets/images/bg/network.png",
      instances: [],
      all_instances: [],
      donor: {
        user_id : null
      },
      is_loading: false,
    };
  },
  created() {
    this.api = this.$Module.$Api.$ApiInstance;
    
    if(this.$route.name == 'IntancePin'){
      this.pin();
    }else
    if(this.$route.name == 'IntanceDisconnect'){
      this.disconnect();
    }else if(this.$route.name == 'IntancePinCancel'){
      this.cancel();
    }else if(this.$route.name == 'IntancePinDelete'){
      this.delete();
    }else if(this.$route.name == 'IntanceEnter'){
      this.enter();
    }else if(this.$route.name == 'IntancePinConfirm'){
      this.confirm();
    }
    else{
      this.load();
    }
  
  },
  computed: {},
  methods: {
    async load() {
      this.is_loading = false;
      const response = await this.api.Instances();

      this.instances = response.instances;
      if(!this.instances.length){
        this.instances = response.all_instances;
        this.instances = this.instances.map(item =>{
          item.not_connection = true
          return item
        })
      }
        
       if(response.donor)
        this.donor = response.donor;
      
      this.is_loading = true;
    }, async pin() {
      this.is_loading = false;
      const response = await this.api.Pin(this.$route.params.id);
     
     
      if(response.goto)this.$router.push(response.goto)
      this.is_loading = true;


    },
    async cancel() {
      this.is_loading = false;
      const response = await this.api.Cancel(this.$route.params.id);
     
      if(response.goto)this.$router.push(response.goto)

      this.is_loading = true;


    },async disconnect() {
       
     localStorage.removeItem('active_instance')
      this.$store.dispatch('setInstancePermissions', null)
     this.$router.push('/instance')
      window.location.reload()


    },
     async delete() {
       debugger
      this.is_loading = false;
      const response = await this.api.DeletePin(this.$route.params.user_id);
     if(response.error) {
      
        this.$root.$emit('Materialert',{
          local : 'message',
          text : response.error.message || response.error,
          isClose : false
        })
        return;
      };
      if(response.goto)this.$router.push(response.goto)

      this.is_loading = true;


    },
    async confirm() {
      this.is_loading = false;
      const response = await this.api.Confirm(this.$route.params.id);
     
      if(response.goto)this.$router.push(response.goto)

      this.is_loading = true;


    },
    enter(){
      
      this.$store.dispatch('setActiveInstance', {
        ...this.$route.params
      })
      
      this.$router.push('/')
    }
  },
};

export default vue;
</script>
