var StoreLanguage = {
  state: {
    translate: {
      userLang: localStorage.getItem('user_lang') != null ? localStorage.getItem('user_lang') : 'en',
      texts: {},
      type: 'info'
    }
  },
  actions: {
    setLanguage: function ({ commit }, data) {
      localStorage.setItem('user_lang', data)
      commit('setLanguage', data)
      commit('loadTranslateTexts')
    },
    loadTranslateTexts ({ commit }) {
      commit('loadTranslateTexts')
    }
  },
  mutations: {
    setLanguage (state, value) {
      state.translate.userLang = value
    },
    loadTranslateTexts (state) {
      state.translate.texts = global.getTexts()
    }

  }
}

module.exports = {
  StoreLanguage
}
