
class Permission{
    get(module, type_permission){
        
        if(type_permission == 'active_instance'){
          //  return store.state.session.active_instance &&  store.state.session.active_instance != 'null'
        }
        if(!store.state.instances || !store.state.instances.listInstances.length){
            return
        }
        var find = false
        if(store.state.instances.listInstances)
        store.state.instances.listInstances.find((instance)=>{
            
            if(store.state.session.active_instance != instance.instance_id) return;
            if(instance.permissions)
            find = instance.permissions.find((permission)=>{
                
                const type = permission.permissions.find((check)=>{
                    return check == type_permission
                })
                return permission.module == module && type
            })

        })
        
        return find
    }

    advance(module, type_permission){
        
        if(!store.state.instances || !store.state.instances.listInstances.length){
            return
        }
        const advance = store.state.instances.listInstances.find((instance)=>{
            var user = JSON.parse(localStorage.getItem('user'))
            console.log("instance:::",instance,user)
            debugger
            return instance.instance.userId == user.id
        })

        if(advance){
            return true
        }

    }
}

export default new Permission()