<template>
  <div
    class="row grades"
    :style="{ backgroundImage: 'url(' + imgBGPage + ')' }"
  >
    <div id="container">  
      <div class="row main-instances">
      
        <!-- Cards container -->
        <div class="col s12 m12 l12">
          <h4>Grades</h4>
          <h5>Ultimas</h5>

          <div id="card-container" class="row">
            <div>
              <!-- Col: Card 1 -->
              <div
                class="col s12 m6 l4"
                v-for="(grade, index) in list"
                v-bind:key="index"
              >
                <!-- Card 1 -->
                <div class="card">
                  <div class="card-content white-text">
                    <div class="card__date">
                      <span class="card__date__day">{{ grade.day }}</span>
                      <span class="card__date__month">{{
                        grade.date_format_month
                      }}</span>
                    </div>
                    <div class="card__meta">
                      <a href="#"
                        ><i class="small material-icons">room</i
                        >{{ grade.client.name }}</a
                      >
                    </div>

                    <span class="card-title grey-text text-darken-4">{{
                      grade.name
                    }}</span>

                    <div
                      v-for="(item, index_item) in grade.items"
                      v-bind:key="index_item"
                      class="row card-row"
                    >
                      <span class="text-darken-2 card-info"
                        ><i class="small material-icons">label</i>&nbsp; ({{
                          item.amount
                        }}) {{ item.product.descricao }} /
                        {{
                          (item.product.valor_uni * item.amount) | toCurrency
                        }}R$</span
                      >
                    </div>
                    <div class="divider"></div>
                    <div class="row card-row">
                      <span class="text-darken-2 card-info"
                        ><i class="small material-icons">add_shopping_cart</i
                        >&nbsp;total produtos:
                        {{ grade.total_amount | toCurrency }}</span
                      >
                    </div>

                    <div class="row card-row">
                      <span class="text-darken-2 card-info"
                        ><i class="small material-icons">attach_money</i
                        >&nbsp;valor total:
                        {{ grade.total_price | toCurrency }}</span
                      >
                    </div>
                    <div class="row card-row">
                      <span class="text-darken-2 card-info"
                        ><i class="small material-icons">event</i>&nbsp;
                        Entrega: {{ grade.execution_date | DateToBRL }}</span
                      >
                    </div>

                    <div class="divider"></div>
                    <h5>Anotação:</h5>
                    <p class="card-subtitle grey-text text-darken-2">
                      {{ grade.note }}
                    </p>
                  </div>

                  <div class="card-action">
                    <a href="#"
                      ><i class="material-icons">&nbsp;language</i>VISIT WEB</a
                    >
                    <a href="#" class="card-action-right"
                      ><i class="material-icons">&nbsp;share</i>SHARE</a
                    >
                  </div>
                </div>
                <!-- End of card -->
              </div>
            </div>
          </div>

          <router-link
            class="btn-floating btn-large waves-effect waves-light red float-btn"
            to="/grades/create"
            ><i class="material-icons">add</i></router-link
          >
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="less">
.grades {
  select {
    display: block;
  }
  .float-btn {
    position: absolute;
    bottom: 5px;
    right: 20px;
  }
  time {
    color: #222222;
  }
  @import url("https://fonts.googleapis.com/css?family=Pacifico");

  body {
    background-color: #f0f0f0;
  }
  h4,
  h5 {
    font-family: "Pacifico", cursive;
  }

  h4 {
    color: #40a8a7;
    /* Turquesa oscuro: color : #0D8080;*/
  }

  h5 {
    /*color : #40a8a7;*/
    color: #ff9900;
  }

  nav .nav-wrapper form,
  nav .nav-wrapper form .input-field {
    height: 100%;
  }

  .card-info,
  .card__meta a {
    color: #3299bb !important;
    display: inline-flex;
    vertical-align: middle;

    font-size: 1.4rem;
  }
  .card__meta i.small {
    font-size: 1.5rem;
  }
  .card .card-content .card-title {
    line-height: 34px;
  }
  .card__date {
    background: #08c;
    position: absolute;
    top: 6px;
    right: 6px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: #fff;
    text-align: center;
    line-height: 13px;
    font-weight: bold;
    padding-top: 7px;
  }
  .card__date__day {
    display: block;
    font-size: 14px;
  }
  .card__date__month {
    display: block;
    font-size: 10px;
    text-transform: uppercase;
  }

  .card .card-content .card-title {
    color: #0d8080 !important;
    line-height: 18px;
  }

  .card .card-title {
    font-size: 18px;
    font-weight: 400;
  }

  .card .card-content p {
    color: #1e1e1e;
    font-size: 13px;
  }

  .collapsible {
    background: #fff;
  }

  .card-row {
    margin-bottom: 4px;
    margin-top: 4px;
  }
  .card-action i {
    vertical-align: text-bottom;
    font-size: 21px;
  }

  .collapsible-header {
    background-color: #3299bb;
    color: #fff;
  }

  .filter-container ul {
    padding: 1rem 2rem;
  }

  .filter-container ul li {
    display: block;
    padding: 16px 0 0;
  }

  .search-title-filter {
    background-color: #3299bb;
    color: #fff;
  }

  nav {
    background-color: #40a8a7;
    border-color: #e7e7e7;
  }

  nav ul > li > a,
  nav .brand-logo {
    color: #fff;
  }

  nav .brand-logo {
    font-size: 1.4em !important;
  }

  #container {
    padding: 20px 10px;

    background-color: rgba(0, 0, 0, 0.8) !important;
    height: 100%;
    overflow: auto;
    scrollbar-color: dark;
  }

  background-color: rgba(0, 0, 0) !important;
  height: 100%;
  #container .main-instances {
    margin: 0px 0px 60px;
  }
}
</style>

<script>
import Modal from './../../../components/modal/Modal'
const monthNames = [
  "Jan",
  "Feb",
  "Mar",
  "Abr",
  "Mai",
  "Jun",
  "Jul",
  "Ago",
  "Set",
  "Out",
  "Nov",
  "Dez",
];

function format_date(date) {
  var data = new Date(date),
    dia = parseInt(data.getDate().toString()),
    dia = dia + 1,
    diaF = dia.length == 1 ? "0" + dia : dia,
    mes = (data.getMonth() + 1).toString(), //+1 pois no getMonth Janeiro começa com zero.
    mesF = mes.length == 1 ? "0" + mes : mes,
    anoF = data.getFullYear();

  return [diaF, mesF, anoF];
}

const vue = {
  components : {Modal},
  data() {
    return {
      imgBGPage: "assets/images/bg/tecnologia.svg",
      imgBGHeader: "assets/images/bg/network.png",
      grade: {},
      list: {},
    };
  },
  created() {
    this.grade = this.$Module.$Api.$ApiGrade;
    document.getElementsByTagName("body")[0].classList.remove("mobile");
    document.getElementsByTagName("body")[0].classList.remove("desktop");
    this.grades();
    // this.$Module.$Init.run(vue)
  },
  computed: {},
  methods: {
    async create() {
      await this.grade.Created({
        name: "Teste3",
        current_date: "1987-05-22 00:00:00.000",
        generate_date: "1987-05-22 00:00:00.000",
        execution_date: "1987-05-22 00:00:00.000",
        stage: "open",
        status: true,
        note: "aa",
        additional_data: "s",
      });

      this.grades();
    },
    async grades() {
      var list = await this.grade.List();

      //document.write("The current month is " + monthNames[d.getMonth()]);

      list = list.map(function (grade) {
        var total_amount = 0;
        var total_price = 0;
        const d = new Date(grade.execution_date);
        console.log(grade);
        grade.day = format_date(grade.execution_date)[0];
        grade.date_format_month = monthNames[d.getMonth()];
        for (var x in grade.items) {
          total_amount += grade.items[x].amount;
          total_price +=
            grade.items[x].amount * grade.items[x].product.valor_uni;
        }
        grade.total_amount = total_amount;
        grade.total_price = total_price;
        return grade;
      });

      this.list = list;
    },

    format_date(date) {
      var data = new Date(date),
        dia = parseInt(data.getDate().toString()),
        dia = dia + 1,
        diaF = dia.length == 1 ? "0" + dia : dia,
        mes = (data.getMonth() + 1).toString(), //+1 pois no getMonth Janeiro começa com zero.
        mesF = mes.length == 1 ? "0" + mes : mes,
        anoF = data.getFullYear();

      return `${mesF}-${diaF}-${anoF}`;
    },
  },
};

export default vue;
</script>
