<template>
  <div class="row user"
   :style="{ backgroundImage: 'url(' + imgBGPage + ')' }"
   >

<div id="container" >
  <div class="row main-instances">
    
    <!-- Cards container -->
    <div class="col s12 m12 l12">

      <h4>Usuários </h4>
      <h5>Lista de usuarios</h5>
      <div id="card-container" class="row">
   
        <div class="col s12 m6 l4" v-for="(item, index) in users" v-bind:key="index">
             <!-- Col: Card -->
        <div class="card text-red red-text" :class="{'orange lighten-4':item.user.not_aprove,'orange lighten-3':item.user.to_accept}" >
            <div class="card-content white-text">
          
             <section>
                <div class="card__meta " >
                  <a ><i class="small material-icons ">business</i> Instance</a>
                  <time>{{new Date() | DateToBRL }}</time>
                </div>

            </section>


              <span class="card-title grey-text text-darken-4">{{item.user.username || '-'}}</span>

              <p class="card-subtitle grey-text text-darken-2">{{item.user.email || '-'}}</p>
              
            </div>
            <div class="card-action ">
              <router-link v-if="item.not_aprove" :to="`/user/aprove/${item.user_id_instance}`" 
              class=" left blue-text center-center"><i class="material-icons">&nbsp;check</i>Aprove</router-link>
              
             <router-link v-if="item.to_accept" :to="`/instance/pin/cancel/${item.user_id_instance}`" 
              class=" left blue-text center-center"><i class="material-icons">&nbsp;check</i>Cancel</router-link>
             
             <router-link v-if="!item.not_aprove && !item.to_accept && permission" :to="`/instance/pin/delete/${item.user_id_instance}`" 
              class=" left blue-text center-center"><i class="material-icons">&nbsp;check</i>Desvincular</router-link>
             
               <router-link v-if="!item.user.to_accept" :to="`/user/${item.user_id_instance}`" class=" right blue-text center-center"><i class="material-icons">&nbsp;fiber_new</i>Editar</router-link>
               
               <router-link v-if="item.user.to_accept" :to="`/user/create/pin/${item.user_id_instance}`" class=" right blue-text center-center"><i class="material-icons">&nbsp;fiber_new</i>Convite</router-link>
               
              <div class="clearfix"></div>
            </div>
          </div>
          </div>
          <!-- End of card -->
        </div>
        <!-- End of col -->
          <router-link
            class="btn-floating btn-large waves-effect waves-light red float-btn"
            to="/user/create/pin"
            ><i class="material-icons">add</i></router-link>
       
      </div>



    </div>
  </div>
</div>


</template>
<style lang="less">

.center-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.user {
  
  .float-btn {
    position: absolute;
    bottom: 5px;
    right: 20px;
  }
  time {
    color: #747474b7;
  }
  .pointer {
    cursor: pointer;
  }
  .icon-add {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  @import url("https://fonts.googleapis.com/css?family=Pacifico");

  body {
    background-color: #f0f0f0;
  }
  h4,
  h5 {
    font-family: "Pacifico", cursive;
  }

  h4 {
    color: #40a8a7;
    /* Turquesa oscuro: color : #0D8080;*/
  }

  h5 {
    /*color : #40a8a7;*/
    color: #ff9900;
  }

  nav .nav-wrapper form,
  nav .nav-wrapper form .input-field {
    height: 100%;
  }

  .card-info,
  .card__meta a {
    color: #3299bb !important;
    display: inline-flex;
    vertical-align: middle;

    font-size: 1rem;
  }
  .card__meta i.small {
    font-size: 1.5rem;
  }
  .card .card-content .card-title {
    line-height: 34px;
  }
  .card__date {
    background: #08c;
    position: absolute;
    top: 6px;
    right: 6px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: #fff;
    text-align: center;
    line-height: 13px;
    font-weight: bold;
    padding-top: 7px;
  }
  .card__date__day {
    display: block;
    font-size: 14px;
  }
  .card__date__month {
    display: block;
    font-size: 10px;
    text-transform: uppercase;
  }

  .card .card-content .card-title {
    color: #0d8080 !important;
    line-height: 18px;
  }

  .card .card-title {
    font-size: 18px;
    font-weight: 400;
  }

  .card .card-content p {
    color: #1e1e1e;
    font-size: 13px;
  }

  .collapsible {
    background: #fff;
  }

  .card-row {
    margin-bottom: 4px;
    margin-top: 4px;
  }
  .card-action i {
    vertical-align: text-bottom;
    font-size: 21px;
  }

  .collapsible-header {
    background-color: #3299bb;
    color: #fff;
  }

  .filter-container ul {
    padding: 1rem 2rem;
  }

  .filter-container ul li {
    display: block;
    padding: 16px 0 0;
  }

  .search-title-filter {
    background-color: #3299bb;
    color: #fff;
  }

  nav {
    background-color: #40a8a7;
    border-color: #e7e7e7;
  }

  nav ul > li > a,
  nav .brand-logo {
    color: #fff;
  }

  nav .brand-logo {
    font-size: 1.4em !important;
  }

  #container {
    padding: 20px 10px;

    background-color: rgba(0, 0, 0, 0.8) !important;
    height: 100%;
    overflow: auto;
    scrollbar-color: dark;
  }

  background-color: rgba(0, 0, 0) !important;
  height: 100%;
  #container .main-instances {
    margin: 0px 0px 60px;
  }
}
</style>

<script>
const vue = {
  data() {
    return {
      imgBGPage: "assets/images/bg/tecnologia.svg",
      imgBGHeader: "assets/images/bg/network.png",
      users: [],
      all_instances: [],
      donor: {
        user_id : null
      },
      is_loading: false,
      permission : false
    };
  },
  created() {
    this.api = this.$Module.$Api.$ApiUser;
    if(this.$route.name == 'IntancePin'){
      this.pin();
    }else if(this.$route.name == 'IntanceCancel'){
      this.cancel();
    }
    else{
      this.load();
    }
  
  },
  computed: {
     
  },
  methods: {
    async load() {
      this.is_loading = false;
      const response = await this.api.Users();
      if(response.error) {
        debugger
        this.$root.$emit('Materialert',{
          local : 'message',
          text : response.error.message || response.error,
          isClose : false
        })
        return;
      };
      this.users = response.instance_user;
      response.instance_user_aproves = response.instance_user_aproves || []
      response.instance_user_to_accept = response.instance_user_to_accept || []
      response.instance_user_aproves.forEach(user => {
        
        user.not_aprove = 1
        this.users.push(user)
      });

       response.instance_user_to_accept.forEach(user => {
        user.to_accept = 1
        this.users.push(user)
      });
     
      this.permission = response.permission || false
      this.is_loading = true;
    }, async pin() {
      this.is_loading = false;
      const response = await this.api.Pin(this.$route.params.id);
     
     
      this.$router.push('/grades')
      this.is_loading = true;


    },
    async cancel() {
      this.is_loading = false;
      const response = await this.api.Cancel(this.$route.params.id);
     
      this.$router.push('/grades')

      this.is_loading = true;


    },
  },
};

export default vue;
</script>
