<template>
  <div class="gyptio-modal-material" v-show="is_open" :grade="grade">
    <!--ModalSelected :ms_position_top="ms_position_top" :ModalSelected_index="ModalSelected_index" :ModalSelected_open="ModalSelected_open"  :callback="modal_selected" /-->
 <h5>Aff {{screen}}</h5>
  
    <div class="modal-mcss grade-create" v-show="screen == 'grade'">
      <div class="card col s12 m6 offset-m3">
        <div class="card-content white-text">
          <div class="card__date red pointer btn-close" v-on:click="close()">
            <i class="sidenav-close white-text"
              ><i class="material-icons">close</i></i
            >
          </div>

          <div class="card-row col s6">
            <div class="">
              <label for="autocomplete-input" class="active"
                ><h5>Nota</h5></label
              >
              <div class="clients input-field col s12 black-text">
                <i class="material-icons prefix">assignment</i>
                <input
                  type="text"
                  id="autocomplete-input"
                  class="autocomplete"
                  autocomplete="off"
                  v-model="grade.name"
                />
              </div>
            </div>
          </div>

          <div class="card-row col s6">
            <div class="">
              <label for="autocomplete-input" class="active"
                ><h5>Cliente</h5></label
              >
              <div class="clients input-field col s12 black-text">
                <i class="material-icons prefix">store</i>
                <input
                  type="text"
                  id="autocomplete-input"
                  class="autocomplete"
                  autocomplete="off"
                  @focus="selected(true)"
                  @blur="selected(false)"
                  v-model="client.name"
                />

                <div class="selected" v-show="is_selected">
                  <ul>
                    <li
                      v-for="(client, index) in clients"
                      v-bind:key="index"
                      @click="client_selected(client)"
                    >
                      {{ client.name }}
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>

          <div class="card-row black-text">
            <div class="section container">
              <ul class="collection">
                <li
                  class=""
                  v-for="(item, index) in grade.items"
                  v-bind:key="index"
                >
                 <div class="clearfix"></div>
                 <div class="divider"></div>
                 <div class="clearfix"></div>
                  <h5 class="indigo-text">
                    {{ item.product.name }}
                    <span class="black-text"
                      >- {{ item.product.price | toBrl }}</span
                    >
                  </h5>

                  <div class="col s6">
                
                    <h5 class="orange-text">Quantidade</h5>
                    <input
                      @keyup.capture="sum_products()"
                      v-model="item.amount"
                    />
                  </div>
                  <div class="col s6">
                   
                    <h5 class="orange-text">Desconto</h5>
                    <input
                      @keyup.capture="sum_products()"
                      v-model="item.discount"
                    />
                  </div>
                   <a class="remove-item secondary-content red-text pull-left" style="float:left">
                    <i
                      class="material-icons mat-red-text"
                      @click="busstop(index)"
                      >edit</i
                    >
                  </a>
                  <a class="remove-item secondary-content red-text">
                    <i
                      class="material-icons mat-red-text"
                      @click="deleteProduct(index)"
                      >delete</i
                    >
                  </a>
                  
                </li>
              </ul>
              <a class="btn add-item pull-right" @click="add_product()"
                >Adc. Produto</a
              >

              <div class="clearfix"></div>
            </div>
          </div>

          <label for="start_date"
            ><h5 class="blue-text">
              Valor total nota:
              <i class="black-text">{{ total_products | toBrl }}</i>
            </h5></label
          >

          <div class="divider"></div>
          <label for="start_date"><h5>Data Entrega:</h5></label>

          <vuejs-datepicker
            id="start_date"
            class="calendar-class"
            :format="'dd-MM-yyyy'"
            v-model="grade.execution_date"
            :value="grade.execution_date | DateToBRL"
          ></vuejs-datepicker>

          <div class="divider"></div>
          <h5>Anotação:</h5>
          <textarea
            class="card-subtitle grey-text text-darken-2 edit-note"
            v-model="grade.note"
          ></textarea>

          <div class="collection">
            <a class="collection-item" @click="edit_stage()"
              >Etapa<span
                class="new badge"
                :class="ColorStage"
                data-badge-caption=""
                >{{ NameStage }}</span
              ></a
            >
          </div>
        </div>
        <div class="card-action">
          <a class="waves-effect waves-light btn pull-right" @click="apply()"
            ><i class="material-icons left">save</i>Aplicar</a
          >

          <div class="clearfix"></div>
        </div>
      </div>

      <div class="bg"></div>
    </div>

    <div class="modal-mcss grade-create" v-if="screen == 'select-item'">
      <div class="card col s12 m6 offset-m3">
        <div class="card-content white-text">
          <div class="card__date red pointer btn-close" v-on:click="close()">
            <i href="#!" class="sidenav-close white-text"
              ><i class="material-icons">close</i></i
            >
          </div>

          <div class="card-row">
            <Item
              :values="products"
              :callback="callback_product"
              title="Etapa"
            />
          </div>

          <div class="divider"></div>
        </div>
      </div>

      <div class="bg"></div>
    </div>

    <div class="modal-mcss grade-create" v-if="screen == 'stage'">
      <div class="card col s12 m6 offset-m3">
        <div class="card-content white-text">
          <div class="card__date red pointer btn-close" v-on:click="close()">
            <i href="#!" class="sidenav-close white-text"
              ><i class="material-icons">close</i></i
            >
          </div>

          <div class="card-row">
            <Item :values="data_stage" :callback="callback" title="Etapa" />
          </div>

          <div class="divider"></div>
        </div>
      </div>

      <div class="bg"></div>
    </div>
  </div>
</template>
<style lang="less">
.calendar-class {
  color: #000;
}
.edit {
  padding: 5px;
}
.clients .selected {
  position: absolute;
  width: 100%;
  margin-top: -15px;
  color: #000;
  z-index: 1;
  ul {
    padding: 5px;
    background: rgba(204, 204, 204, 0.637);

    width: 100%;
    li {
      background: #fefefe;
      margin-left: 5px;
      height: 20px;
      line-height: 20px;
      padding: 5px;
      width: calc(100% -5px);
      cursor: pointer;
    }
    li:hover {
      text-decoration: underline;
    }
  }
}
.pull-right {
  float: right;
  clear: both;
}
.clearfix {
  clear: both;
}

.gyptio-modal-material {
  label {
    font-size: 1.5em;
  }
  .btn-close {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .pointer {
    cursor: pointer;
  }
  height: 100%;
  width: 100%;
  position: fixed;

  z-index: 20001;
  height: 100%;
  width: 100%;
  top: 0px;
  left: 0px;
  .modal-mcss {
    width: 100%;

    .card {
    }
  }
  .modal-mcss .bg {
    top: 0px;
    left: 0px;

    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 20003;
    background-color: rgba(0, 0, 0, 0.8);
  }

  .modal-mcss .card {
    z-index: 20004;
  }
  .modal-mcss {
    display: block;
  }

  .edit-note {
    min-height: 50px;
  }

   .card-content {
    min-height: initial !important;
    max-height: initial !important;
    overflow: initial !important;
  }

}
</style>
<script>
import SelectItem from "./SelectItem";
import Item from "./Item";
import ModalSelected from "./ModalSelected";
//import M from "materialize-css";
import { dirname } from "path";

export default {
  //M,
  name: "Modal",
  props: ["grade_selected", "callback_modal"],
  components: { ModalSelected, SelectItem, vuejsDatepicker, Item },
  data() {
    return {
      callback_product: {},
      formData: {},
      ms_position_top: 0,
      is_open: false,
      screen: "",
      is_selected: false,
      ModalSelected_open: false,
      ModalSelected_index: -1,
      product_index: null,
      NameStage: "",
      ColorStage: "",
      total_products: 0,
      delete: [],
      data_stage: [
        {
          name: "Aberto",
          value: "open",
        },
        {
          name: "Em produção",
          value: "production",
        },
        {
          name: "Ent. Aguardando pagamento",
          value: "delivered_and_waiting_payment",
        },
        {
          name: "Pago para Entregar",
          value: "paid_to_deliver",
        },
        {
          name: "Finalizado",
          value: "paid_and_delivered",
        },
      ],
      client: { name: "" },

      clients: [
        {
          name: "Pão Pão",
        },
        {
          name: "Mercaria marilia",
        },
        {
          name: "Padaria Hebenezer",
        },
      ],
    };
  },
  mounted() {
    let elem = document.querySelector(".datepicker");
    M.Datepicker.init(elem);
  },
  async created() {
    this.grade = this.grade_selected;

    if (!this.grade.items) {
      this.grade = {
        items: [],
      };
    }
    console.log("created", this.grade);
    this.$root.$on("modal", (data) => {
      this.is_open = data.open || true;
      this.screen = data.screen || "";
    });

    var products = await this.$Module.$Api.$ApiProducts.Load();
    this.products = products;
  },
  watch: {
    grade_selected(value) {
      this.grade = this.grade_selected;

      this.grade.execution_date = !this.grade.execution_date
        ? new Date()
        : this.grade.execution_date;
      this.client = this.grade_selected.client;
      this.grade.execution_date = this.grade.execution_date;
      this.setNameStage();
      console.log("open", "::::", value);
      this.sum_products();
    },
    grade(grade) {
      console.log("grade", grade);
    },
    NameStage(value) {
      console.log("NameStage", value);
    },
  },
  methods: {
    setNameStage() {
      var stage = this.data_stage.find((value) => {
        return value.value == this.grade.stage;
      });

      if (!stage) stage = {};
      this.NameStage = stage.name || "Criando";

      var status_stage = this.grade.stage || "creating";

      if (status_stage == "open") {
        this.ColorStage = "orange";
      } else if (status_stage == "production") {
        this.ColorStage = "blue";
      } else if (status_stage == "delivered_and_waiting_payment") {
        this.ColorStage = "red lighten-1";
      } else if (status_stage == "paid_to_deliver") {
        this.ColorStage = "purple";
      } else if (status_stage == "paid_and_delivered") {
        this.ColorStage = "green";
      }
    },
    close() {
      this.is_open = false;
    },
    selected(value) {
      if (!value) {
        setTimeout(() => {
          this.is_selected = value;
        }, 250);
        return;
      }
      this.is_selected = value;
    },
    client_selected(client) {
      this.client = client;
    },
    endEdit() {
      this.$el.querySelector("div").blur();
    },
    updateValue(event) {
      const value = event.target.value;
      console.log(value, this.amount);
      if (String(value).length <= 10) {
        this.amount = value;
      }
      this.$forceUpdate();
    },
    busstop(index) {
     

      this.ModalSelected_open = true;
      this.ModalSelected_index = index;
      this.ItemSelected = this.grade.items[this.ModalSelected_index];
       this.callback_product = this.product
      this.screen = "select-item";
    },
    add_product() {
      this.callback_product = this.apply_add_product;
      this.screen = "select-item";
    },
    apply_add_product(product) {
      var check = false;
      if (this.grade && this.grade.items && this.grade.items.length) {
        check = this.grade.items.filter((el, index) => {
          return (
            el.product.id == product.id && this.ModalSelected_index != index
          );
        });
      }

      if (!check || !check.length) {
        var item = {
          amount: 0,
          discount: 0,
          product,
        };
        this.grade.items.push(item);
      } else {
        M.toast({ html: "Esse item já esta na lista!" });
      }
      this.sum_products();
      this.screen = "grade";
    },
    sum_products() {
      var sum_values = 0;
      if (this.grade.items && this.grade.items.length) {
        const sum = this.grade.items.filter((el, index) => {
          debugger;
          sum_values += (el.amount * el.product.price) - el.discount;
        });
      }

      this.total_products = sum_values;
    },
    product(item) {
      debugger
      const check = this.grade.items.filter((el, index) => {
        return el.id == item.id && this.ModalSelected_index != index;
      });

      if (!check.length) {
        this.grade.items[this.ModalSelected_index].product = item;
      } else {
        M.toast({ html: "Esse item já esta na lista!" });
      }
      this.sum_products();
      this.screen = "grade";
    },
    keyupDiscount(index, product, target) {
      console.log(product, target, $(target).html());
      this.grade.items[index].discount = $(target).html();

      this.sum_products();
    },
    onClickDiscount(index, target) {
      const value = this.grade.items[index].discount;
      if (!value || value == 0) {
        this.grade.items[index].discount = "";
        $(target).html("");
      }
    },
    keyupAmount(index, product, target) {
      console.log(product, target, $(target).html());

      this.grade.items[index].amount = $(target).html();
      this.sum_products();
    },
    keydownAmount(index, product, target) {
      console.log(product, target, $(target).html());
      this.grade.items[index].amount = $(target).html();
      this.sum_products();
    },
    onClickAmount(index, target) {
      const value = this.grade.items[index].amount;
      if (!value || value == 0) {
        this.grade.items[index].amount = "";
        $(target).html("");
      }
    },
    keyupNote(target) {
      this.grade.note = $(target).html();
    },
    onClickNote(target) {},
    edit_stage() {
      this.callback = this.apply_stage;
      this.screen = "stage";
    },
    apply_stage(value) {
      this.grade.stage = value.value;
      this.screen = "grade";

      this.setNameStage();
    },
    deleteProduct(index) {
      this.delete.push(this.grade.items[index].id);
      this.grade.items.splice(index, 1);
    },

    //

    async apply() {
      if (!this.grade.id) {
        this.grade;
        try {
          this.grade.current_date = new Date();
          this.grade.generate_date = new Date();
          this.grade.additional_data = "aditional";
          this.grade.status = 1;
          this.grade.note = this.grade.note == "" ? "-" : this.grade.note;
          const created = await this.$Module.$Api.$ApiGrade.Created(this.grade);
          this.callback_modal();
        } catch (e) {}
      } else {
        this.grade;
        try {
          this.grade.current_date = new Date();
          this.grade.generate_date = new Date();
          this.grade.additional_data = "aditional";
          this.grade.status = 1;
          this.grade.note = this.grade.note == "" ? "-" : this.grade.note;

          const created = await this.$Module.$Api.$ApiGrade.Edit({
            ...this.grade,
            delete: this.delete,
          });
          this.callback_modal();
        } catch (e) {}
      }

      this.is_open = false;
    },
  },
};
</script>