const Api = require('./Api')
class ApiProducts extends Api {
    async Load (sendData) {
      const response = await this.get('/products', sendData)
      const { data } = response
      const { error } = response.data
      if (error) {
        return {
          error
        }
      }
      return data
    }
    async Create (sendData) {
      const response = await this.post('/products', sendData)
      const { data } = response
      const { error } = response.data
      if (error) {
        return {
          error
        }
      }
      return data
    }

    async Edit (sendData) {
      const response = await this.post(`/products/${sendData.id}`, sendData)
      const { data } = response
      const { error } = response.data
      if (error) {
        return {
          error
        }
      }
      return data
    }

}

export const $ApiProducts = new ApiProducts()
