const Api = require('./Api')
class ApiGrade extends Api {
    async List (sendData) {
      const response = await this.get('/grades', sendData)
      const { data } = response
      const { error } = response.data
      if (error) {
        return {
          error
        }
      }
      return data
    }

    async Created (sendData) {
      const response = await this.post('/grades', sendData)
      const { data } = response
      const { error } = response.data
      if (error) {
        return {
          error
        }
      }
      return data
    }

    async Edit (sendData) {
      const response = await this.post(`/grades/${sendData.id}`, sendData)
      const { data } = response
      const { error } = response.data
      if (error) {
        return {
          error
        }
      }
      return data
    }

    
    

}

export const $ApiGrade = new ApiGrade()
