<template>
  <div class="select-item">
      <div class=" input-field col s12 black-text">
                <i class="material-icons prefix">{{icon?icon:'bookmark'}}</i>
                <input
                  type="text"
                  id="autocomplete-input"
                  class="autocomplete"
                  autocomplete="off"
                />

               <div class="selected">
                <ul>
                    <li
                    v-for="(value, index) in values"
                    v-bind:key="index"
                    @click="onSelected(value)"
                    >
                    {{ value.name }}
                    </li>
                </ul>
                </div>
                <label for="autocomplete-input" class="active">{{title}}</label>
              </div>
    
  </div>
</template>
<script>
import './SelectItem.less'
export default {
    props :['values','callback','icon',"title"],
    created(){
console.log('created','vacreatedlues')
       // this.title = this.title | 'Pesquise o nome'
    },
    watch : {
        values(values){
            values.amount = 0;
            console.log('SelectItem',values)
        }
    },
    methods : {
        onSelected(value){
            this.callback(value)
        }
    }
}
</script>