<template>
  <div class="row screen-login"
   :style="{ backgroundImage: 'url(' + imgBGPage + ')' }"
   >
    <div class="filter"></div>
    <div class="main-login col s12" id="main">
      <div class="card">
        <div class="card-image">
          <div class="bg-card"
          :style="{ backgroundImage: 'url(' + imgBGHeader + ')',backgroundPosition:'0px -100px ' }"
          ></div>
          <span class="card-title">Invitation</span>
        </div>
        <div class="card-content">
          <div class="row">
            
            <div class="col s12">
              <div class="row">
                <div class="input-field col s12">
                  <Materialert v-if="message" :message="message" registry="create" :type="type_message" :is_close="false" />
                </div>
                </div>
              <div class="row">
                <div class="input-field col s12">
                  <i class="material-icons prefix">account_circle</i>
                  
                  <label for="icon_prefix" class="active white-text">Usuário ou Email</label>
                  <input id="icon_prefix"  v-model="user.email" disabled />
                </div>
                <div class="input-field col s12">
                  <i class="material-icons prefix">security</i>
                  <input id="icon_telephone" type="password" class="validate" v-model="user.password" />
                  <label for="icon_telephone">Senha</label>
                </div>

        
                 <button class="btn waves-effect waves-light right" type="submit" name="action" v-on:click="apply()">Entrar
                  <i class="material-icons right">send</i>
                </button>
              </div>
            </div>
          </div>
        </div>

         <div class="card-action">
            
            <router-link to="/users/auth" class="">Login</router-link>
          </div>


      </div>
    </div>
  </div>
</template>
<style lang="less">

</style>
<script>

import './Login.less'
const vue = {
    data(){
        return {
            imgBGPage : 'assets/images/bg/tecnologia.svg',
            imgBGHeader : 'assets/images/bg/network.png',
            user : {
              email : '',
              password : '',
            },
            message : '',
            type_message : 'info'
        }
    },
    created(){
     
      this.api = this.$Module.$Api.$ApiUser
      this.load()
    },
    methods : {

        async apply (){
        
          const create = await this.api.CreateUserInvitation(this.$route.params.token,{
            password : this.user.password
          })
          
          if(create.error){
            this.type_message = 'error'
            this.message = create.error.message || create.error
          }else{
            
            this.$router.push(create.goto)
          }
        },

        async load(){
         
          const load = await this.api.LoadInvitation(this.$route.params.token)
          
          if(load && localStorage.getItem('user_id') && load.user.user_id != localStorage.getItem('user_id')){
            alert('Você já esta logado com um usuário diferente do atual, você precisa deslogar do sistema, e entrar novamente com o mesmo convite!')
            this.$router.push('/instance')
            return;
          }
          if(load && localStorage.getItem('user_id') && load.user.user_id == localStorage.getItem('user_id')){
            this.$router.push('/instance')
            return;
          }
          if(load.user){
            this.user = load.user
          }

        }
    }
};

export default vue
</script>
