class=\"alert-heading\">Attention!</h4>\r\n                  </span>\r\n                  <span v-else>\r\n                      <h4 class=\"alert-heading\">Success!</h4>\r\n                  </span>\r\n\r\n                <p>{{message}}</p>\r\n                <span v-if=\"'Não vamos usar por agora'==false\">\r\n                <hr />s\r\n                <p class=\"mb-0\">Whenever you need to, be sure to use margin utilities to keep things nice and tidy.</p>\r\n                 </span>\r\n                </div>\r\n\r\n            <div>\r\n\r\n                    <button type=\"submit\" class=\"btn btn-radius btn-danger \"  @click=\"onCancel\" v-if=\"cancel\" >\r\n                        <i class=\"fa fa-window-close\" ></i> Cancel\r\n                    </button>\r\n                    <button type=\"submit\" class=\"btn btn-radius btn-danger \"  @click=\"onBack\"  v-else-if=\"back\" >\r\n                        <i class=\"fa fa-angle-double-left\" ></i> Back\r\n                    </button>\r\n\r\n                  <button type=\"submit\" class=\"btn btn-radius btn-primary pull-right\"  @click=\"onSuccess\" v-if=\"success\">Confirm\r\n                            <i class=\"fa fa-angle-double-right\" ></i></button>\r\n            </div>\r\n        </div>\r\n    </div>\r\n\r\n</div>\r\n</div>\r\n</template>\r\n<style scoped>\r\n.message-alert {\r\n    height:100% !important;\r\n    width:100% !important;\r\n    float:left;\r\n    position: absolute;\r\n    z-index: 10000;\r\n    background: rgb(0,0,0,0.8);\r\n}\r\n.logout {\r\nbackground: rgb(255,255,255);\r\n}\r\n\r\n.left-type-message .type-message{\r\n    word-wrap:break-word;\r\n\r\n}\r\n\r\n</style>\r\n<script>\r\n\r\nexport default {\r\n\r\n  created () {\r\n    window.addEventListener('keydown', (event) => {\r\n      if (!this.open) {\r\n        return\r\n      }\r\n      if (!this.back) {\r\n        if (event.code === 'Backspace' || event.code === 'Escape') { this.$store.dispatch('setAlertClose') }\r\n      }\r\n    })\r\n  },\r\n  data () {\r\n    return {\r\n      clickInContent: false\r\n    }\r\n  },\r\n  methods: {\r\n    show: () => {\r\n\r\n    },\r\n    OnClickInContent () {\r\n      this.clickInContent = true\r\n    },\r\n    OnCloseBackgroud () {\r\n      if (this.clickInContent === true) {\r\n        this.clickInContent = false\r\n        return\r\n      }\r\n      this.OnClose()\r\n    },\r\n    OnClose () {\r\n      if (this.has_action) {\r\n        return\r\n      }\r\n      if (this.$store.state.alert.goto) {\r\n        this.$router.push(this.$store.state.alert.goto)\r\n      }\r\n\r\n      this.$store.dispatch('setAlertClose')\r\n      this.$store.dispatch('reset')\r\n    },\r\n    onSuccess () {\r\n      this.success()\r\n      this.$store.dispatch('setAlertClose')\r\n      this.$store.dispatch('reset')\r\n    },\r\n    onCancel () {\r\n      this.$store.dispatch('setAlertClose')\r\n\r\n      this.$store.dispatch('reset')\r\n    },\r\n\r\n    onBack () {\r\n      this.$store.dispatch('setAlertClose')\r\n      this.$store.dispatch('reset')\r\n      this.$router.go(-1)\r\n    }\r\n  },\r\n  computed: {\r\n    logout () {\r\n      return this.$route.name === 'Logout'\r\n    },\r\n    has_action () {\r\n      return this.$store.state.alert.cancel || this.$store.state.alert.back || this.$store.state.alert.onSuccess\r\n    },\r\n    type_message () {\r\n      return this.$store.state.alert.type\r\n    },\r\n    message () {\r\n      return this.$store.state.alert.message\r\n    },\r\n    open () {\r\n      return this.$store.state.alert.open\r\n    },\r\n    cancel () {\r\n      return this.$store.state.alert.cancel\r\n    },\r\n    back () {\r\n      return this.$store.state.alert.back\r\n    },\r\n    success () {\r\n      return this.$store.state.alert.onSuccess\r\n    }\r\n  }\r\n}\r\n</script>\r\n"],"sourceRoot":""}]);
