<template>
  <div class="row instances"
   :style="{ backgroundImage: 'url(' + imgBGPage + ')' }"
   >

<div id="container" v-if="is_loading">
   <Materialert :message="message"  :noRadius="true"></Materialert>
  <div class="row main-instances">
    
    <!-- Cards container -->
    <div class="col s12 m12 l12">
      <h4>Aplicações</h4>
      <h5>Suas Conexões</h5>
      <div id="card-container" class="row">
     
        <div class="col s12 m6 l4" v-for="(instance, index) in (user_instances.length?user_instances:all_instances)" v-bind:key="index">
             <!-- Col: Card -->
        <div class="card " :class="{'green lighten-3': $Instance.Connection.current_instance() ==instance.instance_id}" >
         
            <div class="card-content white-text">
              
             <section v-if="instance.instance.userId != user.id">
                <div class="card__meta" v-if="instance.instance.status == 10">
                  <a  @click="messate_teste()"><i class="small material-icons">business</i> <span>Instance</span></a>
                  <time>{{new Date() | DateToBRL }}</time>
                </div>

                <div v-else-if="instance.status < 0" class="card__meta red center" style="padding:5px;margin-bottom:2px;" >
                Solicitação reprovada!
                </div>
                <div v-else-if="instance.status == 1" class="card__meta orange center" style="padding:5px;margin-bottom:2px;" >
                Aguardando aprovação
                </div>
              <div v-else class="card__meta orange center" style="padding:5px;margin-bottom:2px;" >
                Sem resposta(Aprovação)
                </div>
            </section>

              <section v-if="instance.instance.userId == user.id">
                <div class="card__meta" v-if="(instance.instance.status == 10)">
                  <a href="#"><i class="small material-icons">business</i>  <span>Instance</span></a>
                  <time>{{new Date() | DateToBRL }}</time>
                </div>

                <div v-else class="card__meta orange center" style="padding:5px;margin-bottom:2px;" >
                Aguardando ativação
                </div>
            </section>

              <span class="card-title grey-text text-darken-4">{{instance.instance.name || '-'}}</span>

              <p class="card-subtitle grey-text text-darken-2">{{instance.instance.description || '-'}}</p>
              
            </div>
            <div class="card-action " v-if="user_instances.length && !all_instances.length">
              
              <router-link  v-if="$Instance.Connection.current_instance() !=instance.instance.instance_id  && instance.instance.status == 10  &&  instance.status == 10" :to="`/instance/enter/${instance.instance.instance_id}/${instance.user_id_instance}`" class=" left blue-text center-center"><i class="material-icons">&nbsp;home</i>Entrar</router-link>
              <router-link  v-if="$Instance.Connection.current_instance() ==instance.instance.instance_id " :to="`/instance/disconnect`" class=" left blue-text center-center"><i class="material-icons">&nbsp;exit_to_app</i>desconectar</router-link>
              <section v-else-if="instance.instance.userId != user.id">
                 <router-link v-if="instance.status == 0" :to="`/instance/pin/${instance.instance.instance_id}`" class=" left blue-text center-center"><i class="material-icons">&nbsp;fiber_new</i>Conectar!</router-link>
                 <router-link v-else-if="instance.status == 2" :to="`/instance/pin/confirm/${instance.instance.instance_id}`" class=" left blue-text center-center"><i class="material-icons">&nbsp;fiber_new</i>Confirmar</router-link>
                 <router-link v-else :to="`/instance/pin/cancel/${instance.instance.instance_id}`" class=" left blue-text center-center"><i class="material-icons">&nbsp;fiber_new</i>Cancelar</router-link>
              </section>
              
              <router-link v-if="instance.instance.userId == user.id"  :to="`/instance/edit/${instance.instance.instance_id}`"  class=" right blue-text center-center"><i class="material-icons">&nbsp;edit</i>Editar</router-link>
              <div class="clearfix"></div>
            </div>

            <div class="card-action " v-if="!user_instances.length && all_instances.length">
              
             
              <section v-if="instance.instance.userId != user.id">
                 <router-link  :to="`/instance/pin/${instance.instance.instance_id}`" class=" left blue-text center-center"><i class="material-icons">&nbsp;fiber_new</i>Conectar!</router-link>
                
              </section>
              
              <router-link v-if="instance.instance.userId == user.id"  :to="`/instance/edit/${instance.instance.instance_id}`"  class=" right blue-text center-center"><i class="material-icons">&nbsp;edit</i>Editar</router-link>
              <div class="clearfix"></div>
            </div>


          </div>
          </div>
          <!-- End of card -->
        </div>
        <!-- End of col -->

        <div class="col s12 m6 l6" v-if="!user_instances.length">
          <!-- Card 1 -->
          <div class="card">
            <div class="card-content white-text">
             
              <div class="card__meta">
                <a href="#"><i class="small material-icons">fiber_new</i> Conecte-se</a>
                <time>{{new Date() | DateToBRL }}</time>
              </div>
              <span class="card-title grey-text text-darken-4">Conecte-se com um negocio ou crie o seu</span>

              <p class="card-subtitle grey-text text-darken-2">Aqui você pode se conectar com conegocios, criar e customizar suas proprias aplicações e muito mais...</p>
              
            </div>
            <div class="card-action ">
              <router-link to="/instance/pin" class=" left blue-text center-center" v-if="all_instances.length"><i class="material-icons">&nbsp;fiber_new</i>Conectar</router-link>
              <router-link to="/instance/create" class=" right blue-text center-center" v-if="!all_instances.length"><i class="material-icons">&nbsp;fiber_new</i>Criar</router-link>
              <div class="clearfix"></div>
            </div>
          </div>
          <!-- End of card -->
        </div>
      </div>



    </div>
  </div>
</div>


</template>
<style lang="less">

.center-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.instances {
  .card .card-content {
    padding: 14px 12px;
    border-radius: 0 0 2px 2px;
  }
  .card__meta {
    display: flex;
      align-items: center;
    a {
      display: flex;
      align-items: center;
        * {
          padding: 2px;;
        }
    }
  }

  time {
    color: #747474b7;
  }
  .pointer {
    cursor: pointer;
  }
  .icon-add {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  @import url("https://fonts.googleapis.com/css?family=Pacifico");

  body {
    background-color: #f0f0f0;
  }
  h4,
  h5 {
    font-family: "Pacifico", cursive;
  }

  h4 {
    color: #40a8a7;
    /* Turquesa oscuro: color : #0D8080;*/
  }

  h5 {
    /*color : #40a8a7;*/
    color: #ff9900;
  }

  nav .nav-wrapper form,
  nav .nav-wrapper form .input-field {
    height: 100%;
  }

  .card-info,
  .card__meta a {
    color: #3299bb !important;
    display: inline-flex;
    vertical-align: middle;

    font-size: 1rem;
  }
  .card__meta i.small {
    font-size: 1.5rem;
  }
  .card .card-content .card-title {
    line-height: 34px;
  }
  .card__date {
    background: #08c;
    position: absolute;
    top: 6px;
    right: 6px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: #fff;
    text-align: center;
    line-height: 13px;
    font-weight: bold;
    padding-top: 7px;
  }
  .card__date__day {
    display: block;
    font-size: 14px;
  }
  .card__date__month {
    display: block;
    font-size: 10px;
    text-transform: uppercase;
  }

  .card .card-content .card-title {
    color: #0d8080 !important;
    line-height: 18px;
  }

  .card .card-title {
    font-size: 18px;
    font-weight: 400;
  }

  .card .card-content p {
    color: #1e1e1e;
    font-size: 13px;
  }

  .collapsible {
    background: #fff;
  }

  .card-row {
    margin-bottom: 4px;
    margin-top: 4px;
  }
  .card-action i {
    vertical-align: text-bottom;
    font-size: 21px;
  }

  .collapsible-header {
    background-color: #3299bb;
    color: #fff;
  }

  .filter-container ul {
    padding: 1rem 2rem;
  }

  .filter-container ul li {
    display: block;
    padding: 16px 0 0;
  }

  .search-title-filter {
    background-color: #3299bb;
    color: #fff;
  }

  nav {
    background-color: #40a8a7;
    border-color: #e7e7e7;
  }

  nav ul > li > a,
  nav .brand-logo {
    color: #fff;
  }

  nav .brand-logo {
    font-size: 1.4em !important;
  }

  #container {
    padding: 20px 10px;

    background-color: rgba(0, 0, 0, 0.8) !important;
    height: 100%;
    overflow: auto;
    scrollbar-color: dark;
  }

  background-color: rgba(0, 0, 0) !important;
  height: 100%;
  #container .main-instances {
    margin: 0px 0px 60px;
  }
}
</style>

<script>
const vue = {
  data() {
    return {
      imgBGPage: "assets/images/bg/tecnologia.svg",
      imgBGHeader: "assets/images/bg/network.png",
      user_instances: [],
      all_instances: [],
      user : JSON.parse(localStorage.getItem("user")),
      donor: {
        user_id : null
      },
      is_loading: false,
      message : "Bem vindo!!!"
    };
  },
  created() {
    this.api = this.$Module.$Api.$ApiInstance;
    if(this.$route.name == 'IntancePin'){
      this.pin();
    }else if(this.$route.name == 'IntanceCancel'){
      this.cancel();
    }
    else{
      this.load();
    }
  
  },
  computed: {
    alert(){
      return this.$Instance.Materialert['default']
    }
  },
  methods: {
    async load() {
      this.is_loading = false;
      const {instances} = await this.api.Instances();

      this.user_instances = instances || [];

      if(!instances.length){
        const {all_instances} = await this.api.AllInstances();
        this.all_instances = all_instances || [];
        //this.user_instances = all_instances
      }
    
      this.is_loading = true;
    }, async pin() {
      this.is_loading = false;
      const response = await this.api.Pin(this.$route.params.id);
     
     
      this.$router.push('/grades')
      this.is_loading = true;


    },
    async cancel() {
      this.is_loading = false;
      const response = await this.api.Cancel(this.$route.params.id);
     
      this.$router.push('/grades')

      this.is_loading = true;


    },
    
    message_teste(){
       this.$store.dispatch('Materialert',{
         local : 'default',
          isClose : false
        })
    }
  },
};

export default vue;
</script>
