<template>
  <div class="sides" :class="{'side-desktop' : desktop,'side-mobile' : mobile}">
    <div class="side-left">
      <SideLeft />
    </div>
    <div class="side-main">
      <div class="side-top" >
        <SideTop />
      </div>
      <div class="side-right m-css">

       
        <div class="cancel">
          <a 
          @click="side_right()"
          class="btn-floating btn-small waves-effect waves-light red"><i class="material-icons">close</i></a>
       
        </div>


      <ul class="collection with-header items-menu-right" >
        <li class="collection-item pointer" v-if="$Instance.Permission.advance('user','edit')"  @click="to_instance_create()"><div>Novo Server<router-link to="/instance/create" class="secondary-content"><i class="material-icons">videogame_asset</i></router-link></div></li>
        <li class="collection-item pointer" v-if="$Instance.Permission.advance('user','edit')"  @click="to_modules()"><div>Modulos<router-link to="/modules" class="secondary-content"><i class="material-icons">apps</i></router-link></div></li>
        <li class="collection-item pointer"  @click="to('/help')"><div>Ajuda<router-link to="/help" class="secondary-content"><i class="material-icons">help</i></router-link></div></li>
       
      </ul>

      </div>
      <div
        class="main" :class="{'message-is-close':!messageIsClose}"
      >
        <Materialert v-if="!messageIsClose" :message="Materialert.message" noRadius="true" :type="Materialert.type" registry="message" :is_close="messageIsClose" >asd</Materialert>
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>
<style lang="less">
.main.message-is-close{
  height: calc(100% - (var(--sz-vh-top) + 56px));
}

.items-menu-right {
  margin-left: 5px !important;
}
</style>
<script>
/* eslint-disable no-debugger */
import GridProducts from './../grid/GridProducts'

import SideLeft from "@sides/SideLeft";
import SideTop from "@sides/SideTop";
export default {
 
  name: "Side",
  components: {
    SideLeft,
    SideTop,
    GridProducts,
  },
  data() {
    return {
      
      imgBGPage: "assets/images/bg/tecnologia.svg",
      desktop: -1,
      mobile: -1,
      screen: "a",
      Materialert : {
        type : 'default',
        message : ''
      },
      messageIsClose : false,
      router : null,
      
    };
  },
  mounted() {
    this.$nextTick(function () {
      window.addEventListener("resize", this.resize);
    });
  },
  created() {
    const _self = this;
    this.resize();
    this.Materialert = this.$Instance.Materialert || this.Materialert
    this.$root.$on("get-size-screen", () => {
      _self.$root.$emit("resize-screen", {
        screen: _self.desktop ? "desktop" : "mobile",
      });
    });
    setTimeout(() =>{
      this.resize();
    },0)

  this.$root.$on('Materialert',data=>{
    
    this.messageIsClose = data.isClose || false
    this.Materialert.message = data.text || data.message || 'Error'
    this.Materialert.type = data.type || 'default'
    this.router = this.$router
    this.Materialert = {
      ...this.Materialert,
      ...data
    }
  })
        
  this.$root.$emit('Materialert',{
    local : 'message',
    isClose : true,
    message : ''
  })
        
       
  },
  methods: {
    resize() {
      const _self = this;

      document.getElementsByTagName("body")[0].classList.remove("mobile");
      document.getElementsByTagName("body")[0].classList.remove("desktop");
      if (window.innerWidth > 992) {
        _self.desktop = true;
        document
          .getElementsByTagName("body")[0]
          .classList.remove("nav-mobile-active");
      } else {
        _self.desktop = !true;
      }
      _self.mobile = !_self.desktop;
      //document.getElementsByTagName('body')[0].classList.remove('nav-mobile-active')
      console.log(_self.desktop, _self.mobile);
      if (_self.mobile) {
        document.getElementsByTagName("body")[0].classList.add("mobile");
      } else {
        document.getElementsByTagName("body")[0].classList.add("desktop");
      }
    },
    
    side_right(value) {
     if(!value){
          document
          .getElementsByTagName("body")[0]
          .classList.remove("side-right-open");
      }
    },

    to_modules() {
       if(this.$route.name != 'ModuleList')
        this.$router.push('/module')
    },

    to_instance_create() {
       if(this.$route.fullpath != '/instance/create')
        this.$router.push('/instance/create')
    },
    
    to(to){
      if(this.$route.path != to){
        this.$router.push(to)
      }
    }
  },
  watch: {
    mobile(value) {
      if (value) this.$root.$emit("resize-screen", { screen: "mobile" });

      if (value) {
        let sidenav = document.querySelector("#slide-out-mobile");
        M.Sidenav.init(sidenav, {});
      }
    },
    desktop(value) {
      if (value) this.$root.$emit("resize-screen", { screen: "mobile" });
    },

   $route(to, from) {
       
        if(this.router != to){
            this.$root.$emit('Materialert',{
            isClose : true
          })
        }
        // react to route changes...
      }
  },
  computed : {
    message_close(){
      return !this.$Instance.Materialert || !this.$Instance.Materialert.message || this.$Instance.Materialert.message.isClose
    },
    messageTsets(){
    

    }
  }
};
</script>