const { MessageAlert } = require('./MessageAlert')
const { StoreLanguage } = require('./StoreLanguage')
const { StoreApi } = require('./StoreApi')
const { StoreInstances } = require('./StoreInstances')
const { StoreSession } = require('./StoreSession')
const { Materialert } = require('./Materialert')

const ListStores = {
  MessageAlert,
  StoreLanguage,
  StoreApi,
  StoreInstances,
  StoreSession,
  Materialert
}

module.exports = {
  extract ({ state, actions, mutations }) {
    const data = { state, actions, mutations }
    for (var item in ListStores) {
      for (var store in ListStores[item]) {
        for (var s in ListStores[item][store]) {
          data[store][s] = ListStores[item][store][s]
        }
      }
    }

    return { ...data }
  }
}
