<template>
   <span class="token hide">token:{{token}}</span>
</template>
<style scoped>
.token {
    word-break: break-all;
}
</style>
<script>
export default {

  created () {
    
    this.$store.dispatch('loadInstances')
    this.$store.dispatch('loadActiveInstance')
  },
  computed: {
    token () {
      return this.$store.state.session.token
    },
    instances () {
      return this.$store.state.instances.listIntances
    },
    logout () {
      
      return this.$route.name === 'Logout'
    },
    glah () {
      console.log("resize")
    },
    guest () {
    
      this.$store.dispatch('loadToken')
      if(this.$store.state.session.token ==null 
      && this.$route.name !== 'GuestLogin' 
      && this.$route.name !== 'UserAuth'
      && this.$route.name !== 'UserInvitation'){
       this.isGuest()
      }
    },
    active_instance () {
      
      return this.$store.state.session.active_instance
    }
  },
  watch: {
    resize(resize){

    },
    guest (guest){
      if(guest && !this.$store.state.session.token){
       
      }  alert('convidado')
    },
    active_instance (instance) {
      console.log('Instancia ativa', instance)
      /*
      if(instance){
        this.grade = this.$Module.$Api.$ApiInstance.permission()
      }*/
      
    },
    logout (logout) {
   
      if (logout) {
      
          this.logoutUser();
        return;
        global.store.dispatch('setAlert', {
          message: 'Deseja realmente deslogar?',
          type: 'warning',
          onSuccess: () => {
               
            this.logoutUser()
          },
          back: true
        })

        //  this.logoutUser();
      }
    },
    instances (value) {
      console.log('instances', value)
    },
    token (value) {
      if (value !== localStorage.getItem('token')) {
        this.$store.dispatch('setToken', value)
      }
    }
  },
  methods: {
    logoutUser () {
      this.$store.dispatch('setToken', null)
      this.$store.dispatch('setActiveInstance', {logout:true})
      localStorage.removeItem('user')
      localStorage.removeItem('user_id')
      localStorage.removeItem('token')
      localStorage.removeItem('listInstances')
      localStorage.removeItem('active_instance')
      debugger
      document
          .getElementsByTagName("body")[0]
          .classList.remove("nav-mobile-active");
      this.$router.push('/users/auth')
    },
    isGuest(){
      this.logoutUser()
    }
  }
}
</script>
