const Api = require('./Api')
class ApiUser extends Api {
  async set_session(data){

    const { token, id,user } = data
    if(id)
    localStorage.setItem('userId',id)
    if(user)
    localStorage.setItem('user',JSON.stringify(user))
    if(token)
    global.store.dispatch('setToken', token)
  }
    async Auth (sendData) {
      const response = await this.post('/auth', sendData)
      
      const { token, id,user,  origin_instance} = response.data
      const { error } = response.data
      if (error) {
        return {
          error
        }
      }

      if (!token) {
        return {
          error: {
            message: 'Token not found!'
          }
        }
      }
      this.set_session({ token, id : user.id ,user })
      return {
        token,
        origin_instance,
        goto: '/instance'
      }
    }

    async User () {
      const response = await this.get(`/user`)
      const { user } = response.data
      const { error } = response.data
      if (error) {
        return {
          error
        }
      }

      return {
        user
      }
    }

    async Users (sendData) {
      const response = await this.post('/user', sendData)
      const { instance_user,instance_user_aproves,instance_user_to_accept,permission } = response.data
      const { error } = response.data
      if (error) {
        return {
          error
        }
      }

      return {
        instance_user,instance_user_aproves,instance_user_to_accept,permission
      }
    }


    async Aprove (user_id) {
      const response = await this.get(`/user/aprove/${user_id}`)
    
      const { aprove } = response.data
      const { error } = response.data
      if (error) {
        return {
          error
        }
      }

      return {
        aprove
      }
    }

    async Edit (data) {
      const response = await this.post(`/user/edit`,{
        userDataEdit : data
      })
      
      const { user } = response.data
      const { error } = response.data
      if (error) {
        return {
          error
        }
      }
      this.set_session({ user })
      return {
        success : true
      }
    }

    async EditInstance (data,user_id_instance) {
      const response = await this.post(`/user/edit`,data)
    
      const { aprove,user, token } = response.data
      const { error } = response.data
      if (error) {
        return {
          error
        }
      }
      
      return {
        aprove
      }

    }

    async CreateUser  (sendData) {
      var response = null
      try{
         response = await this.post('/register', sendData)
      }catch(e){
        debugger
      }
     

      const { id } = response.data
    
      const { error } = response.data
      if (error) {
        return {
          error
        }
      }

      if (!id) {
        return {
          error: {
            message: 'Error create user!'
          }
        }
      }

      return {
        goto: '/users/auth'
      }
    }


    //Pin User

    //searchs
    async SearchByEmail(email){
      const response = await this.post('/user/search-by-email', {
        email
      })

      const { search,invitation } = response.data

      return {
        search,invitation
      }
    }
    
    async LoadInvitation(token){
      const response = await this.get(`/user/load-invitation?token=${token}`)

      const { user } = response.data
     
      return {
        user
      }
    }


    async CreateUserInvitation(token,sendData) {
      var response = null
      try{
         response = await this.post(`/user/create-invitation?token=${token}`, sendData)
      }catch(e){
        debugger
      }
      const { user } = response.data
      this.set_session({ token : response.data.token, user_id:user.user_id,user })
      return {
        goto: '/instance'
      }
    }
}

export const $ApiUser = new ApiUser()
