<template>
  <div class="user-create-pin container">
    <!-- Cards container -->
    <div class="col s12 m12 l9">
      <h4>Usuário</h4>
      <h5>Convidar</h5>

      <div class="row">
        <form class="col s12">
          
          <div class="">
            <div class="row">
              <div class="input-field col s12">
                <input
                  placeholder="Insira o email"
                  id="first_name"
                  type="text"
                  class="validate"
                  v-model="user.email"
                />
                <label for="first_name" class="active">Email</label>
              </div>
            </div>
            <div class="row">
              <div class="card-action">
                <a class="waves-effect waves-light btn pull-right"
                  @click="search()"
                  ><i class="material-icons left">search</i>Buscar</a
                >
                <div class="clearfix"></div>
              </div>
            </div>
          </div>
<div class = "row" v-if="link_invitation">  
         <div class = "col s12">  
            <div class = "card blue-grey lighten-4">  
               <div class = "card-content">  
                  <span class = "card-title"><h3>Link to share</h3></span>  
                  <p @click="copy_link()" class="cursor-copy"> 
                    <span class="">
                      <i class = "material-icons">content_copy</i>
                      <span class="text-link">{{link_invitation }}</span>
                      </span>
                    </p>  
               </div>  
                 
               <div class = "card-action">  
                  <button class = "cursor-copy btn waves-effect waves-light blue-grey pull-right" @click="copy_link()">  
                     <i class = "material-icons">content_copy</i></button>  
                 
                <div class="clearfix"></div>
               </div>  
            </div>  
         </div>  
           
          
      </div>  



            

          <div class="" v-if="!link_invitation && !token && next">
            <div class="row">
              <div class="input-field col s12">
                <textarea
                  class="materialize-textarea"
                  v-model="user.description"
                ></textarea>
                <label for="email" class="active">Descrição</label>
              </div>
            </div>

            <div class="row">
              <div class="card-action">
                <a class="waves-effect waves-light btn pull-right"
                  @click="apply()"
                  ><i class="material-icons left">save</i>Aplicar</a
                >
                <div class="clearfix"></div>
              </div>
            </div>
          </div>

        </form>
      </div>
    </div>

  
    <div id="notify-copy" class="modal green lighten-1 white-text">
      <div class="modal-content">
        <h4>Sucesso</h4>
        <p>Link copiado com sucesso, envie para o usuário para concluir o vinculo!</p>
      </div>
      <div class="modal-footer ">
        <button href="#!" class="waves-effect waves-light btn  modal-close ">Ok</button>
      </div>
    </div>

  </div>
</template>

<style lang="less">
.user-create-pin {
  .modal {
    overflow: hidden;
  }
  .cursor-copy {
  
    cursor: copy;
  }
  
}
</style>

<script>

//import M from 'materialize-css'
export default {
  data() {
    return {
      user: {
        name: "User",
        description: "Email",
        status: 1,
      },
      link_invitation : '',
      api : {},
      instance_id : null,
      token : '',
      next : false
    };
  },
  created(){
    this.api = this.$Module.$Api.$ApiUser
    this.instance_id = this.$route.params.id || null


    if(this.$route.name == 'UserAprove'){
      this.aprove()
    }
    
    if(this.$route.name == 'UserEdit'){
      this.load()
    }

   // if(this.instance_id)this.load()
  },
  methods : {
    async copy_link(){
      const el = document.createElement('textarea');
      el.value = this.link_invitation;
      document.body.appendChild(el);
      el.select();
      document.execCommand('copy');
      document.body.removeChild(el);
      
     const elem = document.getElementById('notify-copy');
      const instance = M.Modal.init(elem);
      instance.open();

    },
    async aprove(){
   
      await this.api.Aprove(this.$route.params.user_id )

      this.$router.push('/user')
    },
     async load(){
   
      const {user} = await this.api.User(this.$route.params.user_id )
      debugger
      this.user = user
    },
     async apply(){
   
      await this.api.Edit(this.$route.params.user_id,this.user )

      this.$router.push('/user')
    }, async search(){

      this.$root.$emit('Materialert',{
        isClose : true
      })

      const {search,invitation} = await this.api.SearchByEmail(this.user.email)
      
      if(invitation){
        var getUrl = window.location;
        var baseUrl = getUrl .protocol + "//" + getUrl.host

        this.link_invitation = baseUrl + '/'+(window.location.hash.indexOf('#')>=0?'#/':'')+`user/invitation/${invitation.token}`
      }else{
        this.link_invitation = ''
          this.$root.$emit('Materialert',{
            isClose : false,
            text : `O contato ${this.user.email} já esta vinculado no sistema`
          })
      }
    }
  }
};
</script>