s_action) {\r\n        return;\r\n      }\r\n      if (this.$store.state.alert.goto) {\r\n        this.$router.push(this.$store.state.alert.goto);\r\n      }\r\n\r\n      this.$store.dispatch(\"setAlertClose\");\r\n      this.$store.dispatch(\"reset\");\r\n    },\r\n    onSuccess() {\r\n      this.success();\r\n      this.$store.dispatch(\"setAlertClose\");\r\n      this.$store.dispatch(\"reset\");\r\n    },\r\n    onCancel() {\r\n      this.$store.dispatch(\"setAlertClose\");\r\n\r\n      this.$store.dispatch(\"reset\");\r\n    },\r\n\r\n    onBack() {\r\n      this.$store.dispatch(\"setAlertClose\");\r\n      this.$store.dispatch(\"reset\");\r\n      this.$router.go(-1);\r\n    },\r\n  },\r\n  computed: {\r\n    logout() {\r\n      return this.$route.name === \"Logout\";\r\n    },\r\n    has_action() {\r\n      return (\r\n        this.$store.state.alert.cancel ||\r\n        this.$store.state.alert.back ||\r\n        this.$store.state.alert.onSuccess\r\n      );\r\n    },\r\n    type_message() {\r\n      return this.$store.state.alert.type;\r\n    },\r\n    message() {\r\n      return this.$store.state.alert.message;\r\n    },\r\n    open() {\r\n      return this.$store.state.alert.open;\r\n    },\r\n    cancel() {\r\n      return this.$store.state.alert.cancel;\r\n    },\r\n    back() {\r\n      return this.$store.state.alert.back;\r\n    },\r\n    success() {\r\n      return this.$store.state.alert.onSuccess;\r\n    },\r\n  },\r\n};\r\n</script>\r\n"],"sourceRoot":""}]);
