const axios = require('axios')
const baseURL = URL_API ? URL_API : '/api'

const AXIOS_API = axios.create({
  baseURL
})

AXIOS_API.interceptors.request.use(
  async (config) => {
    const token = localStorage.getItem('token')
    if (token) {
      config.headers.Authorization = `Bearer ${token}`
    }
    const active = localStorage.getItem('active_instance')
    if (active) {
      config.headers['Instance'] = `${active}`
    }
    return config
  },
  (error) => {
    return Promise.reject(error)
  }
)
AXIOS_API.interceptors.request.use(
  async (config) => {
    return config
  },
  (error) => {
    return Promise.reject(error)
  }
)

AXIOS_API.interceptors.response.use(
  (response) => {
    return response
  },
  (error) => {
    return Promise.reject(error)
  }
)
class Api {
  constructor () {
    for (var item in AXIOS_API) {
      this[item] = AXIOS_API[item]
    }
  }
}

module.exports = Api
