<template>
  <div class="gyptio-modal-material" v-show="is_open" :product="product">
    
          <!--ModalSelected :ms_position_top="ms_position_top" :ModalSelected_index="ModalSelected_index" :ModalSelected_open="ModalSelected_open"  :callback="modal_selected" /-->
    <div class="modal-mcss grade-create" >
      <div class="card col s12 m6 offset-m3">
        <div class="card-content white-text">
          <div class="card__date red pointer btn-close" v-on:click="OnClose()">
            <i href="#!" class="sidenav-close white-text"
              ><i class="material-icons">close</i></i
            >
          </div>

        
           <div class="card-row ">
        
              <label for="autocomplete-input" class="active"><h5>Produto</h5></label>
              <div class=" input-field col s12 black-text">
                <i class="material-icons prefix">add_shopping_cart</i>
                <input
                  type="text"
                  id="autocomplete-input"
                  class="autocomplete"
                  autocomplete="off"
                  v-model="data.name"
                />
               
              </div>
          </div>
          
          <div class="card-row ">
        
              <label for="autocomplete-input" class="active"><h5>Preço</h5></label>
              <div class=" input-field col s12 black-text">
                <i class="material-icons prefix"></i>
                <input
                  type="text"
                  id="autocomplete-input"
                  class="autocomplete"
                  autocomplete="off"
                  v-model="data.price"
                />
               
              </div>
          </div>
 
          <div class="card-row ">
        
              <label for="autocomplete-input" class="active"><h5>Código Interno</h5></label>
              <div class=" input-field col s12 black-text">
                <i class="material-icons prefix"></i>
                <input
                  type="text"
                  id="autocomplete-input"
                  class="autocomplete"
                  autocomplete="off"
                  v-model="data.internal_code"
                />
               
              </div>
          </div>

          <div class="card-row ">
        
              <label for="autocomplete-input" class="active"><h5>Descrição</h5></label>
              <div class=" input-field col s12 black-text">
                <i class="material-icons prefix"></i>
                <textarea
                  type="text"
                  id="autocomplete-input"
                  class="autocomplete"
                  autocomplete="off"
                  v-model="data.description"
                  style="min-height:50px;"/>
               
              </div>
          </div>


          <div class="clearfix"></div>
          
        </div>
        <div class="card-action">
          <a class="waves-effect waves-light btn pull-right"
          @click="apply()"
            ><i class="material-icons left">save</i>Aplicar</a
          >

          <div class="clearfix"></div>
        </div>
      </div>

      <div class="bg"></div>
    </div>
    
  </div>
</template>
<style lang="less">
.calendar-class {
  color:#000;
}
.edit {
    padding: 5px;
}
.clients .selected {
  position: absolute;
  width: 100%;
  margin-top: -15px;
  color: #000;
  z-index: 1;
  ul {
    padding: 5px;
    background: rgba(204, 204, 204, 0.637);

    width: 100%;
    li {
      background: #fefefe;
      margin-left: 5px;
      height: 20px;
      line-height: 20px;
      padding: 5px;
      width: calc(100% -5px);
      cursor: pointer;
    }
    li:hover {
      text-decoration: underline;
    }
  }
}
.pull-right {
  float: right;
  clear: both;
}
.clearfix {
  clear: both;
}

.gyptio-modal-material {
  overflow: auto;
  label {
    font-size: 1.5em;
  }
  .btn-close {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .pointer {
    cursor: pointer;
  }
  height: 100%;
  width: 100%;
  position: fixed;

  z-index: 20001;
  height: 100%;
  width: 100%;
  top: 0px;
  left: 0px;
  align-items: center;
  .modal-mcss {
    width: 100%;
   

    .card {
    }
  }
  .modal-mcss .bg {
    top: 0px;
    left: 0px;

    position: fixed;
    height: 100%;
    width: 100%;
    z-index: 20003;
    background-color: rgba(0, 0, 0, 0.8);
  }

  .modal-mcss .card {
    z-index: 20004;
  }
  .modal-mcss {
    display: block;
  }
}
</style>
<script>

export default {
  //M,
  name: "Modal",
  props:["open_create","callback_create","value","product","close"],
  components: { },
  data() {
    return {
      data : {
        name : "",
        description : "",
        price : '',
        internal_code : 'c'
      },
      is_open : false,
      api : {}
    };
  },
  created() {
   this.api = this.$Module.$Api.$ApiProducts
     
  },
  watch: {
    value(value){
      
      this.data = value
    },
   open_create(value){
      
       this.is_open = value
   }
  },
  methods: {
    async create (){
      this.data.amount = 0
      this.data.un = 1
      await this.api.Create(this.data)
      this.callback_create()
    },async edit (){
      this.data.amount = 0
      this.data.un = 1
      await this.api.Edit(this.data)
      this.callback_create()
    },
    async apply(){
      debugger
      if(!this.data.id){
        this.create()
      }else{

        await this.edit()
      }
    },
    OnClose(){
      this.close()
    },
  },
};
</script>