<template>
  <div class="selected-modal"  v-show="ModalSelected_open">
    <div class="selected" :style="{Top: 0}">
      <ul>
        <li
          v-for="(client, index) in grade.clients"
          v-bind:key="index"
          @click="value_selected(client)"
        >
          {{ client.name }}{{ms_position_top}}
        </li>
      </ul>
    </div>
  </div>
</template>
<style lang="less">
.selected-modal {
  
  .selected {
    position: absolute;
    width: 250px;
    top: 10px;
    color: #000;
    z-index: 20010;
    ul {
      padding: 5px;
      background: rgba(204, 204, 204, 0.637);

      width: 100%;
      li {
        background: #fefefe;
        margin-left: 0px;
        height: 20px;
        line-height: 20px;
        padding: 5px;
        width: calc(100% -5px);
        cursor: pointer;
      }
      li:hover {
        text-decoration: underline;
      }
    }
  }
}
</style>
<script>
export default {
  props:["ms_position_top","callback","ModalSelected_open","ModalSelected_index"],
  data() {
    return {
      clients: [
        {
          name: "Pão Pão",
        },
        {
          name: "Mercaria marilia",
        },
        {
          name: "Padaria Hebenezer",
        },
      ],
      grade: {
        clients: [
          {
            name: "Pão Pão",
          },
          {
            name: "Mercaria marilia",
          },
          {
            name: "Padaria Hebenezer",
          },
        ],
        products: [
          {
            name: "Coxinha",
            price: 1.9,
          },
          {
            name: "Empada",
            price: 1.9,
          },
          {
            name: "Enroladinho",
            price: 1.9,
          },
        ],
      },
    };
  },
  created (){
    
  },
  methods : {
    value_selected(value){
        this.callback(value)
    }
  }
};
</script>