import Api from './Api.js'
class ApiInstance extends Api {
  async CreateGuest (sendData) {
      const response = await this.post('/instance/CreateGuest', sendData)

      const { success, instance } = response.data
      const { error } = response.data

      if (success) {
        return {
          instance,
          goto: '/users/login'
        }
      }
      if (error) {
        return {
          error
        }
      }
    }

    async Create (sendData) {
      const response = await this.post('/instances/create', sendData)

      const { success, instance } = response.data
      const { error } = response.data

     
      if (error) {
        return {
          error
        }
      }

      return {
        instance,
        goto: '/instance'
      }
    }
    /*
    async permission () {
      const response = await this.get('/permission')

      const { permissions } = response.data

      global.store.dispatch('setInstancePermissions', permissions)
     
    }
*/
    async Edit (instance_id,sendData) {
      const response = await this.post(`/instances/edit/${instance_id}`, sendData)

      const { success, instance } = response.data
      const { error } = response.data

    
      if (error) {
        return {
          error
        }
      }
      return {
        instance,
        goto: '/instance'
      }
    }

    async Instances (sendData) {
      const response = await this.get('/instances')

      const { success, userId, instances,all_instances,donor } = response.data
      const { error } = response.data

      let pending = false;
      instances.filter(function (instance) {
        if (instance.owner_user_id === userId) {
          if (!instance.relational_id || !instance.instance_name) {
            //pending = true  fazendo passar por enquanto
          }
        }
      })
      
      global.store.dispatch('setInstances', instances)
      if (pending === true) {
        return {
          warning: {
            message: 'Você tem instancia pendente de ativação!'
          }
        }
      }
      if (success) {
        return {
          userId,
          alert_disable: true
        }
      }
      if (error) {
        return {
          error
        }
      }

      return {
        instances,
        all_instances,
        donor
      }
    }

    async AllInstances (sendData) {
      const response = await this.get('/instances/all')

      const {  all_instances } = response.data
      const { error } = response.data

      if (error) {
        return {
          error
        }
      }

      return {
        all_instances
      }
    }

    async Instance (instance_id) {
      const response = await this.get(`/instances/${instance_id}`)

      const { instance } = response.data
      const { error } = response.data

      
      if (error) {
        return {
          error
        }
      }

      return {
        instance
      }
    }


    
    async Pin (instance_id) {
      const response = await this.get(`/instances/pin/${instance_id}`)

      const { pin } = response.data
      const { error } = response.data

      
      if (error) {
        return {
          error
        }
      }

      return {
        pin,
        goto : '/instance'
      }
    }

    
    async Cancel (instance_id) {
      const response = await this.get(`/instances/pin/cancel/${instance_id}`)

      const { pin } = response.data
      const { error } = response.data

      
      if (error) {
        return {
          error
        }
      }

      return {
        pin,
        goto : '/instance'
      }
    }

    async DeletePin (user_id) {
      const response = await this.get(`/instances/pin/delete/${user_id}`)

      const { pin } = response.data
      const { error } = response.data

      
      if (error) {
        return {
          error
        }
      }

      return {
        pin,
        goto : '/user'
      }
    }
    
    async Confirm (instance_id) {
      const response = await this.get(`/instances/pin/confirm/${instance_id}`)

      const { pin } = response.data
      const { error } = response.data

      
      if (error) {
        return {
          error
        }
      }

      return {
        pin,
        goto : '/instance'
      }
    }

    async CheckText (domain,instance) {
      const response = await this.post(`/instances/check-txt`,{
        domain,
        instance
      })
      const {error,checked} = response.data

      if (error) {
        return {
          error
        }
      }

      return {
        checked
      }
    }
}
export const $ApiInstance = new ApiInstance()
