<template>
  <div class="instance-create container row">
    <!-- Cards container -->
    <div class="col s12 m12 ">
      <h4>User</h4>
      <h5>Configurações</h5>

      <div class="row">
        <form class="col s12">
          <div class="row">
            <div class="input-field col s12">
              <input
                placeholder="Insira um nome"
                id="first_name"
                type="text"
                class="validate"
                v-model="user.username"
              />
              <label for="first_name" class="active">Username</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <input
                placeholder="Insira seu email"
                id="first_name"
                type="email"
                v-model="user.email" :class="{'invalid' : !is_email, 'valid':is_email}"
              />
              <label for="first_name" class="active" :class="{'invalid' : !is_email, 'valid':is_email}" >Email</label>
              <span :class="{'helper-text' : !is_email}" data-error="Insira um email válido" ></span>
            </div>
          </div>


          <div class="row" >
            <div class="card-action">
              <a class="waves-effect waves-light btn pull-right"
               style="margin-right:10px"
                @click="apply()"
                :class="{'disabled' : (!is_email)}"
                ><i class="material-icons left">save</i>Aplicar</a
              >
              <div class="clearfix"></div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<style lang="less">
</style>

<script>
export default {
  data() {
    return {
      user: {
        name: "User",
        description: "Email",
        password : "",
        new_password : "",
        status: 1,
      },
      api : {},
      instance_id : null,
      change_password : false
    };
  },
  created(){
    this.api = this.$Module.$Api.$ApiUser
    this.instance_id = this.$route.params.id || null


    if(this.$route.name == 'UserAprove'){
      this.aprove()
    }
    
    if(this.$route.name == 'UserEdit'){
      this.load()
    }

   // if(this.instance_id)this.load()
  },
  methods : {
    async aprove(){
   
   try{
      await this.api.Aprove(this.$route.params.user_id )
   }catch(e){

   }
     
      this.$router.push('/user')

    },
     async load(){
   
      const {user} = await this.api.User()
      user.new_password = ""
      this.user = user
    },
     async apply(){
    var response = {};
    if(this.$route.params.user_id_instance){
      response = await this.api.EditInstance(this.user ,this.$route.params.user_id)
    }else{
        response = await this.api.Edit(this.user )
    }
      

      if(response.error){
         this.$root.$emit('Materialert',{
            isClose : false,
            text : response.error.message || response.error.message,
            type : 'error'
          })
        return;
      }
        
       this.$root.$emit('Materialert',{
          isClose : false,
          text : 'Perfil editado com sucesso!',
          type : 'success'
        })
        this.$router.go(this.$router.currentRoute)
        return;
    }

    ,btn_change_password(){
      this.change_password = !this.change_password
    }
  },
  computed : {
    is_email(){
      var email = this.user.email
       const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());

    }
  }
};
</script>