const dataAlertDefault = () => {
  return {
   
  }
}

const alerts = {
  state: {
    Materialert: dataAlertDefault()
  },
  actions: {
   
    Materialert: function ({ commit }, data) {
      commit('Materialertset',data)
    },
    
  },
  mutations: {
    Materialertset (state,data) {
      if(!state.Materialert[data.local]) state.Materialert[data.local] = []
      for(var x in data){
        
        state.Materialert[data.local][x] = data[x]
      }
    }
    
  }
}

module.exports = {
  Materialert: alerts
}
